<template>
  <div name="moc-type">
    <expandable-section-std title="MOC Type" :closedOnStart="false" ref="expandMOCType">
      <div class="data-frame">
        <div class="row">
          <div class="col-md-6">
            <checkbox-std
              v-model="emoc.Normal__c"
              label="Normal"
              name="Normal__c"
              :inline="true"
              :disabled="disabledNextStaEmocRegis"
            />
          </div>
          <div class="col-md-6">
            <checkbox-std
              v-model="emoc.Emergency__c"
              label="Emergency"
              :inline="true"
              :disabled="disabledNextStaEmocRegis"
            />
          </div>
        </div>
      </div>
      <div v-show="checkRequiredMocType" class="slds-form-element slds-has-error">
        <div class="slds-form-element__help" id="error-unique-id-29">
          {{'MOC Type is required'}}
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'moc-type',
  props: [
    'emoc',
    'checkRequiredMocType',
    'disabledNextStaEmocRegis'
  ],
  watch: {
    'emoc.Normal__c' (newValue) {
      if (newValue) {
        this.emoc.Emergency__c = false
      }
    },
    'emoc.Emergency__c' (newValue) {
      if (newValue) {
        this.emoc.Normal__c = false
      }
    }
  }
}
</script>


