<template>
  <div name="emoc-title">
    <expandable-section-std title="E-MOC Title" :closedOnStart="false" ref="expandEmocTitle">
      <div class="data-frame">
        <div class="row">

          <div class="col-12">
            <textarea-std
              v-model="emoc.E_MOC_Title__c"
              v-validate="'required'"
              data-vv-as="E-MOC Title"
              data-vv-value-path="inputValue"
              name="E_MOC_Title__c"
              label="E-MOC Title"
              :error="errors.has('E_MOC_Title__c')"
              :required="true"
              :disabled="disabledNextStaEmocRegis"
              :rows="1"
            >
              <template #error>
                <span>{{ errors.first('E_MOC_Title__c') }}</span>
              </template>
            </textarea-std>
          </div>

        </div>
      </div>
      
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'emoc-title',
  props: [
    'emoc',
    'emocEditEnable',
    'disabledNextStaEmocRegis'
  ]
}
</script>


