<template>
  <div name="lesson-learn">
    <expandable-section-std title="Lesson Learned" ref="expandLessonLearn" :closedOnStart="false">
      <div class="data-frame">
        <label class="slds-form-element__label">
          <abbr v-if="isRamHigh" class="slds-required" title="required">*</abbr>Lesson Learned
        </label>
        <div class="slds-form-element__icon help-text-icon">
          <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
            <i class="fa fa-info-circle" aria-hidden="true"></i>
            <span class="slds-assistive-text">Help</span>
          </button>
          <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-display" role="tooltip" id="help" style="position:absolute;top:-120px;left:-15px;width: 400px;">
            <div class="slds-popover__body">
              Lesson learned can be either good or bad items. The example guideline is as follows<br>
              - Incident from change/any Near miss<br>
              - Change from incidents (corrective action)<br>
              - Change from SCE failure<br>
              - MOC does not meet objectives<br>
              - General problems e.g. unforeseen change, addition budget/resource, etc<br>
            </div>
          </div>
        </div>
        <textarea-std
          v-model="emoc.Lesson_Learn__c"
          v-validate="isRamHigh ? 'required' : ''"
          data-vv-as="Lesson Learned"
          data-vv-value-path="inputValue"
          name="Lesson_Learn__c"
          label=""
          :error="errors.has('Lesson_Learn__c')"
        >
        <!-- :required="isRamHigh" -->
          <template #error>
            <span>{{ errors.first('Lesson_Learn__c') }}</span>
          </template>
        </textarea-std>
        <textarea-std
          v-model="emoc.Recommendation_for_future_improvement__c"
          v-validate="isRamHigh ? 'required' : ''"
          data-vv-as="Recommendation for future improvement"
          data-vv-value-path="inputValue"
          name="Recommendation_for_future_improvement__c"
          label="Recommendation for future improvement"
          :error="errors.has('Recommendation_for_future_improvement__c')"
          :required="isRamHigh"
        >
          <template #error>
            <span>{{ errors.first('Recommendation_for_future_improvement__c') }}</span>
          </template>
        </textarea-std>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'lesson-learned',
  props: [
    'emoc'
  ],
  data () {
    return {}
  },
  computed: {
    isRamHigh () {
      return this.emoc.RAM_Total__c === 'H'
    }
  }
}
</script>


