<script>
export default {
  name: 'category-type',
  props: [
    'emoc',
    'checkRequiredCategoryType',
    'checkRequiredMechanicalChange',
    'checkRequiredInstrumentChange',
    'checkRequiredElectricalChange',
    'checkRequiredCivilChange',
    'checkRequiredIdledRetired',
    'disabledNextStaEmocRegis'
  ],
  computed: {
    checkMechanicalChange () {
      return this.emoc.Mechanical_change__c
    },
    checkInstrumentChange () {
      return this.emoc.Instrument_change__c
    },
    checkElectricalChange () {
      return this.emoc.Electrical_change__c
    },
    checkCivilChange () {
      return this.emoc.Civil_change__c
    },
    checkCatalystChange () {
      return this.emoc.Catalyst_Chemical__c
    },
    checkDesignLimitChange () {
      return this.emoc.Design_limit_IOW_change__c
    },
    checkTankChange () {
      return this.emoc.Tank_change_service__c
    },
    checkIdledRetiredChange () {
      return this.emoc.Idled_Retired_Assets_equipment__c
    },
    showLabelSubCate () {
      return this.checkMechanicalChange || this.checkInstrumentChange || this.checkElectricalChange || this.checkCivilChange || this.checkCatalystChange || this.checkDesignLimitChange || this.checkTankChange || this.checkIdledRetiredChange
    }
    // checkRequiredMechanicalChange () {
    //   // return this.checkMechanicalChange && this.emoc.Mechanical_change__c
    // }
  },
  watch: {
    'emoc.Mechanical_change__c' (newVal) {
      if (!newVal) {
        this.emoc.Air_Cooled_Exchangers__c = false
        this.emoc.Fired_Heaters_Fire_Boiler__c = false
        this.emoc.Pipes_Flanges_Valve__c = false
        this.emoc.Tracing_steam_Steam_trap__c = false
        this.emoc.Pressure_Vessels__c = false
        this.emoc.Rotating_equipment__c = false
        this.emoc.Relief_Systems__c = false
        this.emoc.Shell_Tube_Heat_Exchangers__c = false
        this.emoc.Tanks__c = false
        this.emoc.SBM_CBM__c = false
        this.emoc.HVAC_Refrigeration__c = false
        this.emoc.Loading_arm_Jetty_Lorry__c = false
        this.emoc.Hydraulic_power_unit__c = false
        this.emoc.Safety_Equipment__c = false
        // this.emoc.Golden_weld__c = false
        this.emoc.Hot_tap__c = false
        this.emoc.Temporary_repair__c = false
      }
    },
    'emoc.Instrument_change__c' (newVal) {
      if (!newVal) {
        this.emoc.Alarm__c = false
        this.emoc.APC__c = false
        this.emoc.Base_layer_control_DCS_PLC_TAS_TAM__c = false
        this.emoc.SIF__c = false
        this.emoc.Range__c = false
        this.emoc.Control_valve__c = false
        this.emoc.Instrumentation_equipment__c = false
        this.emoc.QMI__c = false
        this.emoc.Valve__c = false
        this.emoc.Fire_Gas_detection_system__c = false
      }
    },
    'emoc.Electrical_change__c' (newVal) {
      if (!newVal) {
        this.emoc.Electrical_equipment_system__c = false
        this.emoc.Emergency_Backup_Systems__c = false
        this.emoc.Communication__c = false
        this.emoc.Driver_Actuator_process_at_Valve__c = false
      }
    },
    'emoc.Civil_change__c' (newVal) {
      if (!newVal) {
        this.emoc.Buildling_and_Road__c = false
        this.emoc.Structures_foundations__c = false
        this.emoc.Drainage_system_AOC_COC_etc_Bund__c = false
        this.emoc.Refractory_Fireproof__c = false
        this.emoc.Insulation__c = false
        this.emoc.Painting_and_coating__c = false
      }
    },
    'emoc.Idled_Retired_Assets_equipment__c' (newVal) {
      if (!newVal) {
        this.emoc.Demolition__c = false
        this.emoc.Mothballing__c = false
        this.emoc.Preservation_unit__c = false
      }
    }
  }
}
</script>

<style scoped>
  .split-sub-cate {
    border-left: 0.5px solid darkgray;
  }
</style>


