<template>
  <div name="notification">
    <expandable-section-std title="Notification" ref="expandNotification" :closedOnStart="false">
      <div class="data-frame">
        <div v-if="emoc.Notification__r.Name !== ''" class="row">
          <div class="col-md-6">
            <input-text-std v-model="emoc.Notification__r.Name" label="Notification" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <input-text-std v-model="emoc.Notification_Type__c" label="Notification Type" disabled></input-text-std>
          </div>
          <div class="col-md-6">
            <input-text-std v-model="emoc.Order__c" label="Order" disabled></input-text-std>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'notification',
  props: [
    'emoc'
  ]
}
</script>


