<script>
import taChecklistPopup from './TaChecklistPopup.vue'

export default {
  name: 'technical-review',
  props: ['emoc', 'validateSaveCommonWorkflow', 'saveEmoc', 'disabledTa'],
  data () {
    return {
      resultSuccess: {},
      displayChecklist: {
        create: false,
        view: false
      },
      positionSelected: '',
      flgCallSave: false,
      confirmSendback: false,
      msgErrorConfirmSendback: '',
      confirmHold: false,
      msgErrorConfirmHold: '',
      remarkHold: '',
      selectPositionHold: '',
      lookupUserType: 'lookupUser2',
      lookupUserType3: 'lookupUser3',
      lookupUserType4: 'lookupUser4'
    }
  },
  computed: {
    techTaMan () { return this.$store.state.taManMapping },
    lookupTa3 () { return this.$store.state.lookupTa3 },
    taManMappingLength () { return this.$store.state.taManMappingLength },
    ta3ManMaster () { return this.$store.state.ta3ManMaster },
    lookupUser () { return this.$store.state.lookupUser }
  },
  methods: {
    filterTa3ManMaster (searchParam, position) {
      // console.log('filterTa3ManMaster', searchParam, position)
      this.$store.dispatch('receiveTa3ManMaster', { sendParam: searchParam, position: position })
    },
    filterLookupUser2 (type, searchParam) {
      this.$store.dispatch('receiveLookupUser', {type, sendParam: searchParam})
    },
    filterLookupUser3 (type, searchParam) {
      this.$store.dispatch('receiveLookupUser', {type, sendParam: searchParam})
    },
    filterLookupUser4 (type, searchParam) {
      this.$store.dispatch('receiveLookupUser', {type, sendParam: searchParam})
    },
    optionsTa3ManMaster (position) {
      return this.ta3ManMaster[position].filter(df => {
        return this.techTaMan.map(dm => {
          if (dm.Position__c === position) {
            return ''
          } else {
            return dm.TA3_Indicator__c
          }
        }).indexOf(df.Id) === -1
      }).map(dm => {
        return {
          value: dm.Id,
          label: dm.Indicator__c,
          Expert_field_for__c: dm.Expert_field_for__c,
          Indicator__c: dm.Indicator__c,
          TA3_Name__c: dm.TA3_Name__c,
          icon: 'people'
        }
      })
    },
    setDataRow (lookupRow, dataRow) {
      // console.log('lookupRow', lookupRow)
      dataRow.TA3_Expert_Field__c = lookupRow.Expert_field_for__c
      dataRow.TA3_Position_c__c = lookupRow.TA3_Indicator__c
      dataRow.TA3_Name_c__c = lookupRow.TA3_Name__c
      if (typeof dataRow.TA3_Indicator__c === 'undefined' || dataRow.TA3_Indicator__c === '') {
        this.$store.dispatch('receiveTa3ManMaster', { sendParam: dataRow.TA3_Indicator__c, position: dataRow.Position__c })
      }
    },
    optionsLookupUser2 (rowIndex, dataRow) {
      if (typeof this.lookupUser[this.lookupUserType + rowIndex] !== 'undefined') {
        return this.lookupUser[this.lookupUserType + rowIndex].map(dm => {
          dm['value'] = dm.Id
          dm['label'] = dm.FirstName + ' ' + dm.LastName
          dm['icon'] = 'people'
          return dm
        })
      } else {
        this.filterLookupUser2('lookupUser2' + rowIndex, dataRow.Action_by_TA2__c)
        return []
      }
    },
    optionsLookupUser3 (rowIndex, dataRow) {
      if (typeof this.lookupUser[this.lookupUserType3 + rowIndex] !== 'undefined') {
        return this.lookupUser[this.lookupUserType3 + rowIndex].map(dm => {
          dm['value'] = dm.Id
          dm['label'] = dm.FirstName + ' ' + dm.LastName
          dm['icon'] = 'people'
          return dm
        })
      } else {
        this.filterLookupUser3('lookupUser3' + rowIndex, dataRow.Action_by_TA3__c)
        return []
      }
    },
    optionsLookupUser4 (rowIndex, dataRow) {
      if (typeof this.lookupUser[this.lookupUserType4 + rowIndex] !== 'undefined') {
        return this.lookupUser[this.lookupUserType4 + rowIndex].map(dm => {
          dm['value'] = dm.Id
          dm['label'] = dm.FirstName + ' ' + dm.LastName
          dm['icon'] = 'people'
          return dm
        })
      } else {
        this.filterLookupUser4('lookupUser4' + rowIndex, dataRow.Action_by_deviated_form__c)
        return []
      }
    },
    setDataRow2 (rowIndex, dataRow) {
      if (typeof dataRow.Action_by_TA2__c === 'undefined') {
        this.filterLookupUser2('lookupUser2' + rowIndex, '')
      }
    },
    setDataRow3 (rowIndex, dataRow) {
      if (typeof dataRow.Action_by_TA3__c === 'undefined') {
        this.filterLookupUser3('lookupUser3' + rowIndex, '')
      }
    },
    setDataRow4 (rowIndex, dataRow) {
      if (typeof dataRow.Action_by_deviated_form__c === 'undefined') {
        this.filterLookupUser4('lookupUser4' + rowIndex, '')
      }
    },
    setAction2 (rowIndex, dataRow, field) {
      dataRow.Action_by_TA2__c = (dataRow.TA2_s_review_has_been_completed__c === true || dataRow.The_comment_need_to_be_verified__c === true) ? this.emoc.User_Login : ''
      if (typeof dataRow.Action_by_TA2__c !== 'undefined' && dataRow.Action_by_TA2__c !== '') {
        this.filterLookupUser2('lookupUser2' + rowIndex, this.emoc.User_Login)
      }
      if (field === 'TA2_s_review_has_been_completed__c') {
        dataRow.The_comment_need_to_be_verified__c = false
      } else if (field === 'The_comment_need_to_be_verified__c') {
        dataRow.TA2_s_review_has_been_completed__c = false
      }
    },
    setAction3 (rowIndex, dataRow) {
      dataRow.Action_by_TA3__c = (dataRow.TA3_s_review_has_been_completed__c === true) ? this.emoc.User_Login : ''
      if (typeof dataRow.Action_by_TA3__c !== 'undefined' && dataRow.Action_by_TA3__c !== '') {
        this.filterLookupUser3('lookupUser3' + rowIndex, this.emoc.User_Login)
      }
      if (!dataRow.TA3_s_review_has_been_completed__c) {
        dataRow.TA2_s_review_has_been_completed__c = false
        dataRow.Action_by_TA2__c = ''
      }
    },
    setAction4 (rowIndex, dataRow) {
      dataRow.Action_by_deviated_form__c = (dataRow.I_have_uploaded_the_deviation_form__c === true) ? this.emoc.User_Login : ''
      if (typeof dataRow.Action_by_deviated_form__c !== 'undefined' && dataRow.Action_by_deviated_form__c !== '') {
        this.filterLookupUser4('lookupUser4' + rowIndex, this.emoc.User_Login)
      }
      if (!dataRow.I_have_uploaded_the_deviation_form__c) {
        dataRow.Action_by_deviated_form__c = ''
        dataRow.Action_by_TA2__c = ''
        dataRow.TA2_s_review_has_been_completed__c = false
        dataRow.The_comment_need_to_be_verified__c = false
      }
    },
    clearAnotherCheckbox (field, dataRow) {
      if (field === 'Compliance_Standard__c') {
        dataRow.Deviate_Standard__c = false
        dataRow.N_A__c = false
        dataRow.N_A_Remark__c = ''
      } else if (field === 'Deviate_Standard__c') {
        dataRow.Compliance_Standard__c = false
        dataRow.N_A__c = false
        dataRow.N_A_Remark__c = ''
        dataRow.Compliance_Standard_Remark__c = ''
        dataRow.I_have_uploaded_the_deviation_form__c = false
        dataRow.Action_by_deviated_form__c = ''
      } else if (field === 'N_A__c') {
        dataRow.Compliance_Standard__c = false
        dataRow.Deviate_Standard__c = false
        dataRow.Compliance_Standard_Remark__c = ''
      }
      if (!dataRow.Compliance_Standard__c && !dataRow.Deviate_Standard__c && !dataRow.N_A__c) {
        dataRow.Action_by_TA2__c = ''
        dataRow.TA2_s_review_has_been_completed__c = false
        dataRow.The_comment_need_to_be_verified__c = false
      }
    },
    showPopupTaChecklist (actionType) {
      if (actionType === 'Edit') {
        this.validateSaveCommonWorkflow(this.$refs.refModalTaChecklist.onOpen)
      } else {
        this.$refs.refModalTaChecklist.onOpen()
      }
    },
    afterClosePopup () {
      this.flgCallSave = false
      this.displayChecklist.create = false
      this.displayChecklist.view = false
    },
    callBackDocReq () {
      this.$nextTick(() => {
        this.$refs.refModalTaChecklist.onClose()
      })
    },
    callTaChecklist (position) {
      this.positionSelected = position
      this.displayChecklist.create = true
      this.showPopupTaChecklist('Edit')
      this.$store.dispatch('receiveTaChecklist', {Id: this.emoc.Id, Position__c: position})
    },
    callViewTaChecklist (position) {
      this.displayChecklist.view = true
      this.showPopupTaChecklist('View')
      this.$store.dispatch('receiveTaChecklist', {Id: this.emoc.Id, Position__c: position})
    },
    showPopupAfterSave () {
      this.$refs.popupAfterSave.onOpen()
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    callSave () {
      this.flgCallSave = true
      let taRemap = this.$refs.refTaChecklist.taRemap
      let isError = false
      for (var idx1 = 0; idx1 < taRemap.length; idx1++) {
        for (var idx2 = 0; idx2 < taRemap[idx1].Question__c.length; idx2++) {
          if (!taRemap[idx1].Question__c[idx2].Yes__c && !taRemap[idx1].Question__c[idx2].N_A__c) {
            isError = true
            if (!this.$refs.refTaChecklist.$refs['taChecklistExpand' + idx1][0].isOpen) {
              this.$refs.refTaChecklist.$refs['taChecklistExpand' + idx1][0].toggle()
            }
            this.$nextTick(() => {
              let elements = document.getElementsByName(idx1 + 'Yes__c' + idx2)
              this.$nextTick(() => {
                setTimeout(() => elements[0].focus(), 100)
              })
            })
            break
          }
        }
        if (isError) {
          break
        }
      }
      if (!isError) {
        this.showPopupLoader()
        this.$store.dispatch('saveTaChecklist', JSON.stringify(this.$refs.refTaChecklist.genDataSave))
          .then(result => {
            this.closePopupLoader()
            if (result.statusSuccess) {
              this.callBackDocReq()
              this.$nextTick(() => {
                // this.showPopupAfterSave()
                this.resultSuccess = result
                // this.closePopupSuccess()
                this.saveEmoc()
              })
            } else {
              console.log('Error', result)
            }
          })
      }
    },
    btnHoldPopup (dataRow) {
      this.selectPositionHold = dataRow.Position__c
      this.$refs.refHoldPopup.onOpen()
    },
    btnHoldSubmit () {
      this.$validator.validateAll('validate-hold')
        .then((result) => {
          if (result) {
            if (this.confirmHold) {
              let paramHold = {
                emocAppId: this.emoc.Id,
                Position__c: this.selectPositionHold,
                remarkHold: this.remarkHold
              }
              this.$store.dispatch('saveTaHold', paramHold)
                .then(result => {
                  if (result.statusSuccess) {
                    this.showPopupAfterSave()
                    this.resultSuccess = result
                  }
                })
            } else {
              this.msgErrorConfirmHold = 'Please checked to confirm'
            }
          }
        })
    },
    afterCloseHoldPopup () {
      this.remarkHold = ''
      this.confirmHold = false
      this.msgErrorConfirmHold = ''
      this.$nextTick(() => {
        this.errors.clear()
      })
    },
    btnHoldCancel () {
      this.$refs.refHoldPopup.onClose()
    },
    btnSendBackPopup () {
      this.$refs.refSendBackPopup.onOpen()
    },
    btnSendBackSubmit () {
      this.$validator.validateAll('validate-sendback')
        .then((result) => {
          if (result) {
            if (this.confirmSendback) {
              let paramSendBack = {
                emocAppId: this.emoc.Id,
                TA_Sendback_Comment__c: this.emoc.TA_Sendback_Comment__c
              }
              this.$store.dispatch('saveTaSendBack', paramSendBack)
                .then(result => {
                  if (result.statusSuccess) {
                    this.showPopupAfterSave()
                    this.resultSuccess = result
                  }
                })
            } else {
              this.msgErrorConfirmSendback = 'Please checked to confirm'
            }
          }
        })
    },
    afterCloseSendBackPopup () {
      this.emoc.TA_Sendback_Comment__c = ''
      this.confirmSendback = false
      this.msgErrorConfirmSendback = ''
      this.$nextTick(() => {
        this.errors.clear()
      })
    },
    btnSendBackCancel () {
      this.$refs.refSendBackPopup.onClose()
    }
  },
  components: {
    taChecklistPopup
  }
}
</script>

<style scoped>
  .padding-l40 {
    padding-left: 40px;
  }
</style>


