<template>
  <div name="consolidate-comment-popup">
    <div class="data-frame">
      <div class="scrollbar-table">
        <table id="consolidate-comment-popup">
          <tr>
            <th width="20%">Attachment No</th>
            <th width="40%">E-MOC Doc Type</th>
            <th width="40%">E-MOC Checlist type</th>
          </tr>
          <tr v-for="conCom in consolidateComment">
            <td>{{conCom.Name}}</td>
            <td>{{conCom.E_MOC_Doc_Type__c}}</td>
            <td>{{conCom.E_MOC_Checklist_type__c}}</td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'consolidate-comment-popup',
  computed: {
    consolidateComment () { return this.$store.state.consolidateComment }
  }
}
</script>

<style scoped>
  #consolidate-comment-popup td, #consolidate-comment-popup th {
    border: 1px solid #ddd;
    padding: 5px;
  }
  #consolidate-comment-popup th {
    background-color: #fafaf9;
    padding-top: 5px;
    padding-bottom: 5px;
    /* text-align: center; */
  }
  #consolidate-comment-popup td>.type-list {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    min-width: 150px;
    max-width: 190px;
  }
  #consolidate-comment-popup td>.question-list {
    overflow: hidden;
    text-overflow: ellipsis;
    /* white-space: nowrap; */
    min-width: 400px;
    max-width: 400px;
  }
  #consolidate-comment-popup tr:hover {background-color: #f2f2f2;}
  #consolidate-comment-popup .slds-form-element__label {display: none;}
  #consolidate-comment-popup .slds-checkbox_faux {margin-right: 0px;}
  .scrollbar-table {
    overflow: auto;
  }
  .column-revision { min-width: 50px; text-align: center; }
  .column-last-modified { min-width: 200px; text-align: center; }
</style>


