<template>
  <div name="change-type">
    <expandable-section-std title="Change Type" ref="expandChangeType" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div v-if="!checkRecordTypeFn" class="col-md-6">
            <div class="row">
              <div class="col-4">
                <checkbox-std
                  v-model="emoc.Permanent__c"
                  label="Permanent"
                  name="Permanent__c"
                  :inline="true"
                  :disabled="disabledNextStaEmocRegis"
                />
              </div>
            </div>
            <div v-if="emoc.Permanent__c" class="row">
              <div class="col-sm-6">
                <label class="slds-form-element__label">
                  <abbr class="slds-required" title="required">*</abbr>Required Completion date
                </label>
                <div v-show="emoc.Common_Workflow__c" class="slds-form-element__icon help-text-icon">
                  <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
                    <i class="fa fa-info-circle" aria-hidden="true"></i>
                    <span class="slds-assistive-text">Help</span>
                  </button>
                  <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-display" role="tooltip" id="help" style="position:absolute;top:-50px;left:-15px;width: 175px;">
                    <div class="slds-popover__body">
                      RAM = L approx. 90 days<br>
                      RAM = M approx. 60 days<br>
                      RAM = H approx. 30 days<br>
                    </div>
                  </div>
                </div>
                <v-menu v-model="showPickerComplDate"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="emoc.Completion_date__c"
                      v-validate="'required|date_format:dd/MM/yyyy'"
                      data-vv-as="Completion date"
                      data-vv-value-path="inputValue"
                      name="Completion_date__c"
                      :error="errors.has('Completion_date__c')"
                      @blur="dateCompletion = parseDate(emoc.Completion_date__c)"
                      placeholder="dd/mm/yyyy"
                      v-bind="attrs"
                      v-on="on"
                      append-icon="mdi-calendar"
                      autocomplete="off"
                      hide-details
                      outlined
                      dense
                      :disabled="disabledChangeType"

                    ></v-text-field>
                  </template>
                  <v-date-picker
                    no-title
                    v-model="dateCompletion"
                    @change="showPickerComplDate = false"
                    :min="minCalendar"
                  ></v-date-picker>
                </v-menu>
                <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('Completion_date__c') }}</span></div>
              </div>
            </div>
          </div>
          <div :class="checkRecordTypeFn ? 'col-md-12' : 'col-md-6'">
            <div class="row">
              <div class="col-6">
                <checkbox-std
                  v-model="emoc.Temporary__c"
                  label="Temporary"
                  :inline="true"
                  :disabled="checkRecordTypeFn || disabledNextStaEmocRegis"
                />
              </div>
            </div>
            <!-- v-if="this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c" -->
            <div  v-if="emoc.Temporary__c && (emoc.Common_Workflow__c || emoc.MOS_for_maintenance__c || emoc.Safeguarding_force_signal__c)" class="row">
              <div class="col-sm-6">
                <label class="slds-form-element__label">
                  <abbr class="slds-required" title="required">*</abbr>Required Start date
                </label>
                <div v-show="emoc.Common_Workflow__c" class="slds-form-element__icon help-text-icon">
                  <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
                    <i class="fa fa-info-circle" aria-hidden="true"></i>
                    <span class="slds-assistive-text">Help</span>
                  </button>
                  <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-display" role="tooltip" id="help" style="position:absolute;top:-50px;left:-15px;width: 175px;">
                    <div class="slds-popover__body">
                      RAM = L approx. 90 days<br>
                      RAM = M approx. 60 days<br>
                      RAM = H approx. 30 days<br>
                    </div>
                  </div>
                </div>
                <v-menu v-model="showPickerStartDate"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="emoc.Start_date__c"
                      v-validate="'required|date_format:dd/MM/yyyy'"
                      data-vv-as="Start date"
                      data-vv-value-path="inputValue"
                      name="Start_date__c"
                      :error="errors.has('Start_date__c')"
                      @blur="dateStart = parseDate(emoc.Start_date__c)"
                      placeholder="dd/mm/yyyy"
                      v-bind="attrs"
                      v-on="on"
                      append-icon="mdi-calendar"
                      autocomplete="off"
                      hide-details
                      outlined
                      dense
                      :disabled="disabledChangeType"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    no-title
                    v-model="dateStart"
                    @change="showPickerStartDate = false"
                    :min="minCalendar"
                  ></v-date-picker>
                </v-menu>
                <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('Start_date__c') }}</span></div>
              </div>
              <div class="col-sm-6">
                <label class="slds-form-element__label">
                  <abbr class="slds-required" title="required">*</abbr>Required End date
                </label>
                <div class="slds-form-element__icon help-text-icon">
                  <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
                    <i class="fa fa-info-circle" aria-hidden="true"></i>
                    <span class="slds-assistive-text">Help</span>
                  </button>
                  <div class="slds-popover slds-popover_tooltip slds-nubbin_bottom-left help-text-display" role="tooltip" id="help" style="position:absolute;top:-50px;left:-15px;width: 175px;">
                    <div class="slds-popover__body">
                      {{emoc.Functional_Override__c ? 'Maximum 6 months' : 'Not more than next MTA'}}
                    </div>
                  </div>
                </div>
                <v-menu v-model="showPickerEndDate"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="emoc.End_date__c"
                      v-validate="'required|date_format:dd/MM/yyyy|after:' + checkDateAfter"
                      data-vv-as="End date"
                      data-vv-value-path="inputValue"
                      name="End_date__c"
                      :error="errors.has('End_date__c')"
                      @blur="dateEnd = parseDate(emoc.End_date__c)"
                      placeholder="dd/mm/yyyy"
                      v-bind="attrs"
                      v-on="on"
                      append-icon="mdi-calendar"
                      autocomplete="off"
                      hide-details
                      outlined
                      dense
                      :disabled="disabledChangeType"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    no-title
                    v-model="dateEnd"
                    @change="showPickerEndDate = false"
                    :min="minCalendar"
                    :max="maxEndDate"
                  ></v-date-picker>
                </v-menu>
                <div class="slds-has-error"><span class="slds-form-element__help">{{ errors.first('End_date__c') }}</span></div>
              </div>
            </div>
            <div v-if="checkRecordTypeFn" class="row">
              <div class="col-md-12">
                <textarea-std
                  v-model="emoc.Reason_of_Work__c"
                  v-validate="'required'"
                  data-vv-as="Reason of Work"
                  data-vv-value-path="inputValue"
                  name="Reason_of_Work__c"
                  label="Reason of Work"
                  :error="errors.has('Reason_of_Work__c')"
                  :required="true"
                  :rows="1"
                  :disabled="disabledNextStaEmocRegis"
                >
                  <template #error>
                    <span>{{ errors.first('Reason_of_Work__c') }}</span>
                  </template>
                </textarea-std>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-show="checkRequiredChangeType" class="slds-form-element slds-has-error">
        <div class="slds-form-element__help" id="error-unique-id-29">
          {{'Change Type is required'}}
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
import moment from 'moment'

export default {
  name: 'change-type',
  props: [
    'emoc',
    'checkRequiredChangeType',
    'recordType',
    'disabledNextStaEmocRegis',
    'disabledChangeType'
  ],
  data () {
    return {
      showPickerComplDate: false,
      showPickerStartDate: false,
      showPickerEndDate: false,
      dateCompletion: '',
      dateStart: '',
      dateEnd: '',
      nameRules: [
        v => !!v || 'Name is required'
      ],
      minCalendar: moment().format('YYYY-MM-DD')
    }
  },
  created () {
    if (this.checkRecordTypeFn) {
      this.emoc.Temporary__c = true
      this.emoc.Start_date__c = moment().format('DD/MM/YYYY')
    }
  },
  computed: {
    setRuleDateStart () {
      if (this.emoc.Functional_Override__c) {
        if (this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c) {
          return 'required|date_format:dd/MM/yyyy'
        } else {
          return 'date_format:dd/MM/yyyy'
        }
      } else {
        return 'required|date_format:dd/MM/yyyy'
      }
    },
    setRuleDateEnd () {
      if (this.emoc.Functional_Override__c) {
        if (this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c) {
          return 'required|date_format:dd/MM/yyyy|date_between:' + this.checkDateAfter + ',' + this.checkDateMax
        } else {
          return 'date_format:dd/MM/yyyy'
        }
      } else {
        return 'required|date_format:dd/MM/yyyy|after:' + this.checkDateAfter
      }
    },
    checkDateAfter () {
      return moment(this.emoc.Start_date__c, 'DD/MM/YYYY').add(-1, 'days').format('DD/MM/YYYY')
    },
    checkDateMax () {
      return moment(this.emoc.Start_date__c, 'DD/MM/YYYY').add(181, 'days').format('DD/MM/YYYY')
    },
    maxEndDate () {
      if (this.emoc.Functional_Override__c) {
        let endDate = moment(this.emoc.Start_date__c, 'DD/MM/YYYY').add(180, 'days').format('DD/MM/YYYY')
        return moment(endDate, 'DD/MM/YYYY').format('YYYY-MM-DD')
      } else {
        return ''
      }
    },
    checkRecordTypeFn () {
      return this.recordType === 'Functional Override'
    }
  },
  watch: {
    checkRecordTypeFn (newVal) {
      if (newVal) {
        this.emoc.Temporary__c = true
        this.emoc.Start_date__c = moment().format('DD/MM/YYYY')
      }
    },
    dateCompletion (val) {
      this.emoc.Completion_date__c = this.formatDate(this.dateCompletion)
    },
    dateStart (val) {
      this.emoc.Start_date__c = this.formatDate(this.dateStart)
    },
    dateEnd (val) {
      this.emoc.End_date__c = this.formatDate(this.dateEnd)
    },
    'emoc.Permanent__c' (newValue) {
      if (newValue) {
        this.emoc.Temporary__c = false
      }
    },
    'emoc.Temporary__c' (newValue) {
      if (newValue) {
        this.emoc.Permanent__c = false
      }
    }
  },
  methods: {
    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    }
  }
}
</script>

<style scoped>
  .custom-icon-help-text {
    top: 0px;
  }
</style>


