<template>
  <div name="ta2">
    <expandable-section-std title="TA2" :closedOnStart="false" ref="expandTa2">
      <div class="data-frame">
        <div class="row">
          <div class="col-sm-6">
            <div class="slds-border_bottom topic2">
              <label>TA Mandatory</label>
            </div>
            <div v-for="mappingDefault in taManMappingDefault" class="row">
              <div class="col-sm-6">
                <input-text-std v-model="mappingDefault.TA2_Indicator__c" label="Indicator / Name" disabled></input-text-std>
              </div>
              <div class="col-sm-6">
                <input-text-std v-model="mappingDefault.TA2_Expert_Field__c" label="Expert field" disabled></input-text-std>
              </div>
            </div>
          </div>
          <div class="col-sm-6">
            <div class="row">
              <div class="col-sm-6 padding-b0">
                <div class="slds-border_bottom topic2">
                  <label>Additional TA2</label>
                </div>
              </div>
              <div class="col-sm-6 padding-b0">
                <button style="float: right; top: 4px;"  class="slds-button slds-button_neutral" @click="$refs.linkTaManGuide.click()">
                  TA Mandatory Guideline
                </button>
                <a hidden ref="linkTaManGuide" :href="emoc.TA_Mandatory_Guideline__c" target="_blank">{{emoc.TA_Mandatory_Guideline__c}}</a>
              </div>
            </div>

            <div v-for="(mappingOptional, index) in taManMappingOptional" class="row margin-t0" :key="index">
              <div v-if="!disabledTa" class="col-1 delete-mapping-optional" :class="{'delete-mapping-optional-disabled': mappingOptional.flgDelete}">
                <i class="fa fa-trash-o icon-delete-mapping-optional" @click="clickDelete(mappingOptional.rowId)" aria-hidden="true"></i>
              </div>
              <div class="col-sm-5">
                <combobox-std
                  v-model="mappingOptional.TA2_Indicator__c"
                  v-validate="!mappingOptional.flgDelete ? 'required' : ''"
                  data-vv-as="Indicator / Name"
                  data-vv-value-path="textInputValue"
                  :name="'TA2_Indicator__c' + index"
                  :error="errors.has('TA2_Indicator__c' + index)"
                  :options="taManMasterOption(index)"
                  @ddlSelected="ddlSelected(mappingOptional, arguments[0])"
                  label="Indicator / Name"
                  :disabled="mappingOptional.flgDelete || disabledTa"
                >
                  <template #error>
                    <span>{{ errors.first('TA2_Indicator__c' + index) }}</span>
                  </template>
                </combobox-std>
              </div>
              <div class="col-sm-6">
                <input-text-std v-model="mappingOptional.TA2_Expert_Field__c" label="Expert field" disabled></input-text-std>
              </div>
            </div>
            <template v-if="!disabledTa">
              <div v-show="!hideBtnAdd" class="row">
                <div class="col-sm-6">
                  <button class="slds-button slds-button_neutral" @click="clickAdd"><i class="fa fa-plus" aria-hidden="true"></i>&nbsp;&nbsp;Add</button>
                </div>
              </div>
            </template>
          </div>
        </div>
        <!-- <div class="row">
          <div class="col-sm-6">
            <checkbox-std v-show="taReviewbyTechnologistDisabled"
              v-model="taReviewbyTechnologistDisabled"
              label="TA selection confirmed by Technologist Manager"
              class="select-ta-review"
              :inline="true"
              disabled
            />
            <checkbox-std v-show="!taReviewbyTechnologistDisabled"
              v-model="emoc.TA_Review_by_Technologist_Manager__c"
              label="TA selection confirmed by Technologist Manager"
              class="select-ta-review"
              :inline="true"
              :disabled="taTechnologistDisabled"
            />
          </div>
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Action_by_Technologist__r.Name" label="Action by" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <checkbox-std v-show="taReviewbyOperationDisabled"
              v-model="taReviewbyOperationDisabled"
              label="TA selection confirmed by Operation Manager"
              class="select-ta-review"
              :inline="true"
              disabled
            />
            <checkbox-std v-show="!taReviewbyOperationDisabled"
              v-model="emoc.TA_Review_by_Operation_Manager__c"
              label="TA selection confirmed by Operation Manager"
              class="select-ta-review"
              :inline="true"
              :disabled="taOperationDisabled"
            />
          </div>
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Action_by_Operation__r.Name" label="Action by" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <checkbox-std v-show="taReviewbyEngineerDisabled"
              v-model="taReviewbyEngineerDisabled"
              label="TA selection confirmed by Maintenance Manager"
              class="select-ta-review"
              :inline="true"
              disabled
            />
            <checkbox-std v-show="!taReviewbyEngineerDisabled"
              v-model="emoc.TA_Review_by_Engineer_Manager__c"
              label="TA selection confirmed by Maintenance Manager"
              class="select-ta-review"
              :inline="true"
              :disabled="taEngineerDisabled"
            />
          </div>
          <div class="col-sm-6">
            <input-text-std v-model="emoc.Action_by_Maintenance__r.Name" label="Action by" disabled></input-text-std>
          </div>
        </div> -->
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'ta2',
  props: ['emoc', 'disabledTa'],
  created () {
    this.$store.dispatch('receiveTaManMapping', this.emoc.Id)
    this.$store.dispatch('receiveTaManMaster', this.emoc.Id)
  },
  computed: {
    taManMaster () { return this.$store.state.taManMaster },
    taManMapping () { return this.$store.state.taManMapping },
    taReviewbyTechnologistDisabled () { return this.$store.state.taReviewbyTechnologistDisabled },
    taReviewbyOperationDisabled () { return this.$store.state.taReviewbyOperationDisabled },
    taReviewbyEngineerDisabled () { return this.$store.state.taReviewbyEngineerDisabled },
    taManMappingDefault () {
      return this.taManMapping.filter(df => {
        return df.Require__c === 'Require'
      })
    },
    taManMappingOptional () {
      return this.taManMapping.filter((df, idx) => {
        df['rowId'] = idx
        return df.Require__c === 'Optional'
      })
    },
    taTechnologistDisabled () {
      return (this.emoc.TA_Review_by_Operation_Manager__c && !this.taReviewbyOperationDisabled) || (this.emoc.TA_Review_by_Engineer_Manager__c && !this.taReviewbyEngineerDisabled)
    },
    taOperationDisabled () {
      return (this.emoc.TA_Review_by_Technologist_Manager__c && !this.taReviewbyTechnologistDisabled) || (this.emoc.TA_Review_by_Engineer_Manager__c && !this.taReviewbyEngineerDisabled)
    },
    taEngineerDisabled () {
      return (this.emoc.TA_Review_by_Technologist_Manager__c && !this.taReviewbyTechnologistDisabled) || (this.emoc.TA_Review_by_Operation_Manager__c && !this.taReviewbyOperationDisabled)
    },
    hideBtnAdd () {
      return this.taManMappingOptional.length === this.taManMaster.length
    }
  },
  watch: {
    'emoc.TA_Review_by_Technologist_Manager__c' (newVal) {
      if (newVal && !this.taReviewbyTechnologistDisabled) {
        this.emoc.Action_by_Technologist__c = this.emoc.UpdById
      } else {
        this.emoc.Action_by_Technologist__c = ''
      }
    },
    'emoc.TA_Review_by_Operation_Manager__c' (newVal) {
      if (newVal && !this.taReviewbyOperationDisabled) {
        this.emoc.Action_by_Operation__c = this.emoc.UpdById
      } else {
        this.emoc.Action_by_Operation__c = ''
      }
    },
    'emoc.TA_Review_by_Engineer_Manager__c' (newVal) {
      if (newVal && !this.taReviewbyEngineerDisabled) {
        this.emoc.Action_by_Maintenance__c = this.emoc.UpdById
      } else {
        this.emoc.Action_by_Maintenance__c = ''
      }
    }
  },
  methods: {
    taManMasterOption (index) {
      let retOption = this.taManMaster.filter(df => {
        return this.taManMappingOptional.map(dm => {
          return dm.TA2_Indicator__c
        }).indexOf(df.TA2_Indicator__c) === -1 || this.taManMappingOptional[index].TA2_Indicator__c === df.TA2_Indicator__c
      }).map(dm => {
        return {value: dm.TA2_Indicator__c, label: dm.TA2_Indicator__c}
      })
      return retOption
    },
    clickAdd () {
      this.$validator.validateAll()
        .then((result) => {
          if (result) {
            let lastIndex = this.taManMapping.length
            this.taManMapping.push({Id: null, E_MOC_Application__c: this.emoc.Id, TA2_Indicator__c: '', Require__c: 'Optional', TA_Level__c: '2', Position__c: '', rowId: lastIndex, TA2_Expert_Field__c: '', TA2_Name__c: '', TA2_Position__c: '', TA3_Indicator__c: '', TA3_Expert_Field__c: '', TA3_s_review_has_been_completed__c: '', Action_by_TA3__c: ''})
          }
        })
    },
    clickDelete (rowId) {
      let setTaManMapping = JSON.parse(JSON.stringify(this.taManMapping))
      setTaManMapping[rowId]['flgDelete'] = !setTaManMapping[rowId]['flgDelete']
      this.$nextTick(() => {
        this.$store.commit('setTaManMapping', setTaManMapping)
      })
    },
    ddlSelected (dataRow, selectValue) {
      let taManMasterFilter = this.taManMaster.filter(df => {
        return df.TA2_Indicator__c === selectValue
      })
      if (taManMasterFilter.length > 0) {
        dataRow.TA2_Expert_Field__c = taManMasterFilter[0].TA2_Expert_Field__c
        dataRow.Position__c = taManMasterFilter[0].Position__c
        dataRow.TA2_Name__c = taManMasterFilter[0].TA2_Name__c
      }
    }
  }
}
</script>

<style scoped>
  .delete-mapping-optional {
    font-size: large;
    color: #0176d3;
    padding-top: 38px;
    text-align: end;
  }
  .delete-mapping-optional-disabled {
    color: rgb(234, 0, 30);
  }
  .delete-mapping-optional
    .icon-delete-mapping-optional {
      cursor: pointer;
    }
  .slds-input-has-icon_right .slds-input {
    text-decoration: line-through;
  }
  .select-ta-review
    .slds-form-element__control .slds-checkbox {
      top: 25px;
    }
  .padding-b0 {
    padding-bottom: 0px;
  }
  .margin-t0 {
    margin-top: 0px;
  }
</style>


