<template>
  <div name="risk-analysis">
    <expandable-section-std title="Risk Analysis" :closedOnStart="false" ref="expandRiskAnalysis">
      <div class="data-frame">
        <div class="row">
          <div class="col-sm-6">
            <checkbox-std v-model="emoc.HAZOP__c"
                label="HAZOP"
                name="HAZOP__c"
                :inline="true"
                :disabled="disabledRiskAnalysis"
            />
          </div>
          <div v-show="emoc.HAZOP__c" class="col-sm-3">
            <button v-show="emoc.HAZOP_Checklist_Link__c !== ''" class="slds-button slds-button_neutral" @click="$refs.linkHazopRef.click()">HAZOP Checklist Link</button>
            <a hidden ref="linkHazopRef" :href="emoc.HAZOP_Checklist_Link__c" target="_blank">{{emoc.HAZOP_Checklist_Link__c}}</a>
          </div>
          <div v-show="emoc.HAZOP__c" class="col-sm-3">
            <button v-show="emoc.HAZOP_Recommendation__c !== ''" class="slds-button slds-button_neutral" @click="$refs.linkHazopRecomRef.click()">HAZOP Recommendation Response Sheet Form</button>
            <a hidden ref="linkHazopRecomRef" :href="emoc.HAZOP_Recommendation__c" target="_blank">{{emoc.HAZOP_Recommendation__c}}</a>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <checkbox-std v-model="emoc.HRA__c"
                label="HRA"
                :inline="true"
                :disabled="disabledRiskAnalysis"
            />
          </div>
          <div v-show="emoc.HRA__c" class="col-sm-3">
            <button v-show="emoc.HRA_Checklist_Link__c !== ''" class="slds-button slds-button_neutral" @click="$refs.linkHraRef.click()">HRA Checklist Link</button>
            <a hidden ref="linkHraRef" :href="emoc.HRA_Checklist_Link__c" target="_blank">{{emoc.HRA_Checklist_Link__c}}</a>
          </div>
          <div v-show="emoc.HRA__c" class="col-sm-3">
            <button v-show="emoc.HRA_Recommendation__c !== ''" class="slds-button slds-button_neutral" @click="$refs.linkHraRecomRef.click()">HRA Recommendation Response Sheet Form</button>
            <a hidden ref="linkHraRecomRef" :href="emoc.HRA_Recommendation__c" target="_blank">{{emoc.HRA_Recommendation__c}}</a>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <checkbox-std v-model="emoc.Environmental_Impact__c"
                label="Environmental Impact"
                :inline="true"
                :disabled="disabledRiskAnalysis"
            />
          </div>
          <div v-show="emoc.Environmental_Impact__c" class="col-sm-3">
            <button v-show="emoc.Environmental_Impact_Checklist_Link__c !== ''" class="slds-button slds-button_neutral" @click="$refs.linkEnvRef.click()">Environmental Impact Checklist Link</button>
            <a hidden ref="linkEnvRef" :href="emoc.Environmental_Impact_Checklist_Link__c" target="_blank">{{emoc.Environmental_Impact_Checklist_Link__c}}</a>
          </div>

          <div v-show="emoc.Environmental_Impact__c" class="col-sm-3">
            <button v-show="emoc.EA_Recommendation__c !== ''" class="slds-button slds-button_neutral" @click="$refs.linkEaRecomRef.click()">EA Recommendation Response Sheet Form</button>
            <a hidden ref="linkEaRecomRef" :href="emoc.EA_Recommendation__c" target="_blank">{{emoc.EA_Recommendation__c}}</a>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <checkbox-std v-model="emoc.Reliability_Checklist__c"
                label="Reliability Checklist"
                :inline="true"
                :disabled="disabledRiskAnalysis"
            />
          </div>
          <div v-show="emoc.Reliability_Checklist__c" class="col-sm-3">
            <button v-show="emoc.Reliability_Checklist_Link__c !== ''" class="slds-button slds-button_neutral" @click="$refs.linkRelRef.click()">Reliability Checklist Link</button>
            <a hidden ref="linkRelRef" :href="emoc.Reliability_Checklist_Link__c" target="_blank">{{emoc.Reliability_Checklist_Link__c}}</a>
          </div>
          <div v-show="emoc.Reliability_Checklist__c" class="col-sm-3">
            <button v-show="emoc.Reliability_Recommendation__c !== ''" class="slds-button slds-button_neutral" @click="$refs.linkRelRecomRef.click()">Reliability Recommendation Response Sheet Form</button>
            <a hidden ref="linkRelRecomRef" :href="emoc.Reliability_Recommendation__c" target="_blank">{{emoc.Reliability_Recommendation__c}}</a>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-9"></div>
          <div class="col-sm-3">
            <button v-show="emoc.Summary_sheet__c !== ''" class="slds-button slds-button_neutral" @click="$refs.linkSummarySheetRef.click()">Summary sheet</button>
            <a hidden ref="linkSummarySheetRef" :href="emoc.Summary_sheet__c" target="_blank">{{emoc.Summary_sheet__c}}</a>
          </div>
        </div>

        <div class="row" v-if="emoc.flgShowConfirmeRiskAnalysis">
          <div class="col-md-6">
            <checkbox-std
              v-model="emoc.I_have_confirmed_Risk_Analysis__c"
              label="I have confirmed Risk Analysis already uploaded into this E-MOC."
              :inline="true"
              :disabled="disabledRiskAnalysis" 
              @onChange="setActionBy(emoc.I_have_confirmed_Risk_Analysis__c)"
            />
          </div>
          <div class="col-md-6">
            <lookups-std v-model="emoc.Action_by_risk_analysis__c"
                         label="Action by"
                         placeholder="Search..."
                         :options="optionsLookupUser()"
                         name="Action_by_risk_analysis"
                         @search="filterLookupUser(arguments[0])"
                         icon="people"
                         disabled
                         loading>
            </lookups-std>
          </div>
        </div>

        <div v-show="checkRequiredRiskAnalysis" class="slds-form-element slds-has-error">
          <div class="slds-form-element__help" id="error-unique-id-29">
            {{'Risk Analysis is required'}}
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'risk-analysis',
  props: [
    'emoc', 'checkRequiredRiskAnalysis', 'disabledRiskAnalysis'
  ],
  data () {
    return {
      nameRules: [
        v => !!v || 'Name is required'
      ],
      lookupType: 'riskAnalysis'
    }
  },
  created () {
    if (this.emoc.Action_by_risk_analysis__c !== '' && this.emoc.Action_by_risk_analysis__c) {
      this.filterLookupUser(this.emoc.Action_by_risk_analysis__c)
    }
  },
  computed: {
    lookupUser () { return this.$store.state.lookupUser }
  },
  watch: {
    'emoc.Action_by_risk_analysis__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser(newVal)
      }
    }
  },
  methods: {
    setActionBy (val) {
      this.emoc.Action_by_risk_analysis__c = (this.emoc.I_have_confirmed_Risk_Analysis__c === true) ? this.emoc.User_Login : ''
      if (typeof this.emoc.Action_by_risk_analysis__c !== 'undefined' && this.emoc.Action_by_risk_analysis__c !== '') {
        this.filterLookupUser(this.emoc.User_Login)
      }
    },
    filterLookupUser (searchParam) {
      this.$store.dispatch('receiveLookupUser', {type: this.lookupType, sendParam: searchParam})
    },
    optionsLookupUser () {
      if (typeof this.lookupUser[this.lookupType] !== 'undefined') {
        return this.lookupUser[this.lookupType].map(dm => {
          return {
            value: dm.Id,
            label: dm.FirstName + ' ' + dm.LastName,
            icon: 'people'
          }
        })
      } else {
        return []
      }
    }
  }
}
</script>
<style scoped>
  .float-r {
    float: right;
  }
</style>


