<template>
  <div name="functional-location-associate">
    <expandable-section-std title="Associate Functional Location" ref="expandFunctionLocationAssociate" :closedOnStart="true" helpText="For adding reference Functional Location in case of multi disciplines MOC.">
      <div class="data-frame">
        <template v-for="(funcLoca, idx) in emocFuncLoca">
          <div class="row">
            <div v-show="!disabledNextStaEmocRegis" class="col-1 delete-mapping-optional" :class="{'delete-mapping-optional-disabled': funcLoca.flgDelete}">
              <i class="fa fa-trash-o icon-delete-mapping-optional" @click="clickDelete(idx)" aria-hidden="true"></i>
            </div>
            <div v-show="!funcLoca.flgDelete" :class="disabledNextStaEmocRegis ? 'col-12' : 'col-11'">
              <div class="row">
                <div class="col-md-6">
                  <lookups-std v-model="funcLoca.Functional_Location__c"
                                v-validate="!funcLoca.flgDelete ? 'required' : ''"
                                data-vv-as="Functional Location"
                                data-vv-value-path="textInputValue"
                                label="Functional Location"
                                placeholder="Search..."
                                :options="optionsLookupFunctionalLocation(idx, funcLoca)"
                                :error="errors.has('Asso_Functional_Location__c' + idx)"
                                :name="'Asso_Functional_Location__c' + idx"
                                @search="filterLookupFunctionalLocation('lookupFnc' + idx, arguments[0])"
                                @setDataRow="setDataRow(idx, arguments[0])"
                                icon="account"
                                :disabled="disabledNextStaEmocRegis || funcLoca.flgDelete"
                                loading>
                    <template #error>
                      <span>{{ errors.first('Asso_Functional_Location__c' + idx) }}</span>
                    </template>
                  </lookups-std>
                </div>
                <div class="col-md-6">
                  <input-text-std v-model="funcLoca.Functional_Location_Description__c" label="Functional Location Description" disabled></input-text-std>
                </div>
              </div>
              <div class="row">
                <div class="col-md-6">
                  <input-text-std v-model="funcLoca.Plant_of_Main_Work_Center__c" label="Plant of Main Work Center" disabled></input-text-std>
                </div>
                <div class="col-md-6">
                  <input-text-std v-model="funcLoca.Main_Work_Center__c" label="Main Work Center" disabled></input-text-std>
                </div>
              </div>
            </div>
          </div>
        </template>
        <div v-show="!disabledNextStaEmocRegis" class="row">
          <div class="col-sm-6">
            <button class="slds-button slds-button_neutral" @click="clickAdd"><i class="fa fa-plus" aria-hidden="true"></i>&nbsp;&nbsp;Add</button>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'functional-location-associate',
  props: [
    'emoc',
    'emocEditEnable',
    'disabledNextStaEmocRegis'
  ],
  data () {
    return {
      lookupFncType: 'lookupFnc'
    }
  },
  computed: {
    lookupFncLocation () { return this.$store.state.lookupFncLocation },
    lookupUser () { return this.$store.state.lookupUser },
    emocFuncLoca () { return this.$store.state.emocFuncLoca }
  },
  methods: {
    filterLookupFunctionalLocation (type, searchParam) {
      this.$store.dispatch('receiveLookupFncLocation', {type, sendParam: searchParam})
    },
    optionsLookupFunctionalLocation (rowIndex, dataRow) {
      if (typeof this.lookupFncLocation[this.lookupFncType + rowIndex] !== 'undefined') {
        return this.lookupFncLocation[this.lookupFncType + rowIndex].map(dm => {
          dm['value'] = dm.Id
          dm['label'] = dm.Name
          dm['icon'] = 'account'
          return dm
        })
      } else {
        this.filterLookupFunctionalLocation(this.lookupFncType + rowIndex, dataRow.Functional_Location__r.Name)
        return []
      }
    },
    setDataRow (rowIndex, dataRow) {
      this.emocFuncLoca[rowIndex].Functional_Location_Description__c = dataRow.Description__c
      this.emocFuncLoca[rowIndex].Main_Work_Center__c = dataRow.MainWorkCenter_Text__c
      this.emocFuncLoca[rowIndex].Plant_of_Main_Work_Center__c = dataRow.mainWorkCenterPlant_Text__c
      if (typeof dataRow.Functional_Location__c === 'undefined') {
        this.filterLookupFunctionalLocation(this.lookupFncType + rowIndex, '')
      }
    },
    clickAdd () {
      this.$validator.validateAll()
        .then((result) => {
          if (result) {
            this.emocFuncLoca.push({
              Id: '',
              E_MOC_Application__c: this.emoc.Id,
              Functional_Location__c: '',
              Functional_Location__r: {
                Name: ''
              },
              Functional_Location_Description__c: '',
              Main_Work_Center__c: '',
              Plant_of_Main_Work_Center__c: ''
            })
            this.filterLookupFunctionalLocation(this.lookupFncType + (this.emocFuncLoca.length - 1), '')
          }
        })
    },
    clickDelete (rowIndex) {
      let emocFuncLoca = JSON.parse(JSON.stringify(this.emocFuncLoca))
      emocFuncLoca[rowIndex]['flgDelete'] = !emocFuncLoca[rowIndex]['flgDelete']
      this.$nextTick(() => {
        this.$store.commit('setEmocFuncLoca', emocFuncLoca)
      })
    }
  }
}
</script>

<style scoped>
  .delete-mapping-optional {
    font-size: large;
    color: #0176d3;
    padding-top: 38px;
    max-width: 3.333333%;
    text-align: center;
  }
  .delete-mapping-optional-disabled {
    color: rgb(234, 0, 30);
  }
  .delete-mapping-optional
    .icon-delete-mapping-optional {
      cursor: pointer;
    }
</style>


