<template>
  <div name="notification">
    <expandable-section-std title="Notification" ref="expandNotification" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-md-6">
            <input-text-std v-model="emoc.Notification__c" label="Notification" disabled></input-text-std>
          </div>
          <div class="col-md-6">
            <input-text-std v-model="emoc.Integration_Status__c" label="SAP Integration Status" disabled></input-text-std>
          </div>

          <div class="col-md-6">
            <input-text-std v-model="emoc.Notification_Type__c" label="Notification Type" disabled></input-text-std>
          </div>
          <div class="col-md-6">
            <input-text-std v-model="emoc.Notification_Status__c" label="Notification Status" disabled></input-text-std>
          </div>
     
          <div class="col-md-6">
            <lookups-std v-model="emoc.Requestor_Name__c"
                         data-vv-as="Requester"
                         data-vv-value-path="inputValue"
                         label="Requester"
                         placeholder="Search..."
                         :options="optionsLookupUser"
                         name="Requestor_Name__c"
                         @search="filterLookupUser(arguments[0])"
                         icon="people"
                         disabled
                         loading>
            </lookups-std>
          </div>
          <div class="col-md-6">
            <input-text-std v-model="emoc.Department_Resp__c" label="Department Resp" disabled></input-text-std>
          </div>
          <div class="col-md-6">
            <input-text-std v-model="emoc.Requestor_ID__c" label="Requestor ID" disabled></input-text-std>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'notification',
  props: [
    'emoc',
    'emocEditEnable'
  ],
  data () {
    return {
      value: null
    }
  },
  created () {
    if (this.emoc.Requestor_Name__c !== '' && this.emoc.Requestor_Name__c) {
      this.filterLookupUser(this.emoc.Requestor_Name__c)
    }
  },
  watch: {
    'emoc.Requestor_Name__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser(newVal)
      }
    }
  },
  computed: {
    lookupUser () { return this.$store.state.lookupUser },
    optionsLookupUser () {
      return this.lookupUser.map(dm => {
        return {
          value: dm.Id,
          label: dm.FirstName + ' ' + dm.LastName,
          icon: 'people'
        }
      })
    }
  },
  methods: {
    filterLookupUser (searchParam) {
      this.$store.dispatch('receiveLookupUser', searchParam)
    }
  }
}
</script>


