<template>
  <div name="emoc-common-workflow">
    <emoc-title                     :emoc="emoc"
                                    :emocEditEnable="emocEditEnable"
                                    :disabledNextStaEmocRegis="disabledNextStaEmocRegis2"
                                    ref="emocTitleCmp" />

    <functional-location            :emoc="emoc"
                                    :emocEditEnable="emocEditEnable"
                                    :disabledNextStaEmocRegis="disabledNextStaEmocRegis"
                                    ref="functionalLocationCmp" />

    <functional-location-associate  :emoc="emoc"
                                    :emocEditEnable="emocEditEnable"
                                    :disabledNextStaEmocRegis="disabledNextStaEmocRegis2"
                                    ref="functionalLocationCmpAssociate" />

    <budget-type                    :emoc="emoc"
                                    :checkRequiredBudgetType="checkRequiredBudgetType"
                                    :showNotification="showNotification"
                                    ref="budgetTypeCmp" />

    <category-type                  :emoc="emoc"
                                    :checkRequiredCategoryType="checkRequiredCategoryType"
                                    :checkRequiredMechanicalChange="checkRequiredMechanicalChange"
                                    :checkRequiredInstrumentChange="checkRequiredInstrumentChange"
                                    :checkRequiredElectricalChange="checkRequiredElectricalChange"
                                    :checkRequiredCivilChange="checkRequiredCivilChange"
                                    :checkRequiredIdledRetired="checkRequiredIdledRetired"
                                    :disabledNextStaEmocRegis="disabledNextStaEmocRegis"
                                    ref="categoryTypeCmp" />

    <moc-type                       :emoc="emoc"
                                    :checkRequiredMocType="checkRequiredMocType"
                                    :disabledNextStaEmocRegis="disabledNextStaEmocRegis"
                                    ref="mocTypeCmp" />
              
    <change-type                    :emoc="emoc"
                                    :checkRequiredChangeType="checkRequiredChangeType"
                                    :disabledNextStaEmocRegis="disabledNextStaEmocRegis"
                                    :disabledChangeType="disabledChangeType"
                                    :recordType="'Common Workflow'"
                                    ref="changeTypeCmp" />

    <input-change-detail            :emoc="emoc"
                                    :disabledNextStaEmocRegis="disabledNextStaEmocRegis2"
                                    ref="inputChangeDetailCmp" />

    <template v-if="statusHazardScreening || statusTechnicalReview">
      <moc-focal-point              v-show="showSectionMocFocalPoint"
                                    :emoc="emoc"
                                    :showSectionMocFocalPoint="showSectionMocFocalPoint"
                                    ref="mocFocalPointCmp" />
                                    
      <risk-analysis                v-if="showHazardChecklist"
                                    :emoc="emoc"
                                    :checkRequiredRiskAnalysis="checkRequiredRiskAnalysis"
                                    :disabledRiskAnalysis="disabledRiskAnalysis"
                                    ref="riskAnalysisCmp" />
    </template>

    <ram-rating                     :emoc="emoc"
                                    :showHazardChecklist="showHazardChecklist"
                                    :disabledRamRating="disabledRamRating"
                                    :disabledRamRatingInitial="disabledRamRatingInitial"
                                    ref="ramRatingCmp" />
    <template v-if="!(statusHazardScreening || statusTechnicalReview)">
      <moc-focal-point              v-show="showSectionMocFocalPoint"
                                    :emoc="emoc"
                                    :showSectionMocFocalPoint="showSectionMocFocalPoint"
                                    ref="mocFocalPointCmp" />
    
      <risk-analysis                v-if="showHazardChecklist"
                                    :emoc="emoc"
                                    :checkRequiredRiskAnalysis="checkRequiredRiskAnalysis"
                                    :disabledRiskAnalysis="disabledRiskAnalysis"
                                    ref="riskAnalysisCmp" />
    </template>

    <ta2                            v-if="showHazardChecklist && emoc.Show_TA_Mandatory__c"
                                    :emoc="emoc"
                                    :disabledTa="disabledTa"
                                    ref="ta2Cmp" />

    <technical-review               v-if="showTechnicalReview"
                                    :emoc="emoc"
                                    :validateSaveCommonWorkflow="validateSaveCommonWorkflow"
                                    :saveEmoc="saveEmoc"
                                    :disabledTa="disabledTa"
                                    ref="technicalReviewCmp" />

    <ta-review-moc-focal            v-if="showTechnicalReview && emoc.Show_Technical_review_by_MOC__c"
                                    :emoc="emoc"
                                    :disabledTechnicalReviewMoc="disabledTechnicalReviewMoc"
                                    ref="technicalReviewMocCmp" />

    <pssr-category                  v-if="showPssrCategory"
                                    :emoc="emoc"
                                    :validateSaveCommonWorkflow="validateSaveCommonWorkflow"
                                    :saveEmoc="saveEmoc"
                                    :disabledPssrCategory="disabledPssrCategory"
                                    ref="pssrCategoryCmp" />

    <moc-accomplishment             v-if="showMocAccomplishment"
                                    :emoc="emoc"
                                    :disabledMocAccomplishment="disabledMocAccomplishment"
                                    :checkRequiredMocAccomplishment="checkRequiredMocAccomplishment"
                                    ref="mocAccomplishmentCmp" />

    <lesson-learned                  v-if="showLessonLearned"
                                    :emoc="emoc"
                                    ref="lessonLearnedCmp" />

  </div>
</template>
<script>
import emocTitle from './EmocTitle.vue'
import budgetType from './BudgetType.vue'
import functionalLocation from './FunctionalLocation.vue'
import functionalLocationAssociate from './FunctionalLocationAssociate.vue'
import categoryType from './CategoryType.vue'
import mocType from './MOCType.vue'
import changeType from './ChangeType.vue'
import inputChangeDetail from './InputChangeDetail.vue'
import ramRating from './RamRating.vue'
import mocFocalPoint from './MocFocalPoint.vue'
import riskAnalysis from './RiskAnalysis.vue'
import ta2 from './Ta2.vue'
import technicalReview from './TechnicalReview.vue'
import taReviewMocFocal from './TaReviewMocFocal.vue'
import notification from './Notification.vue'
import pssrCategory from './PssrCategory.vue'
import lessonLearned from './LessonLearned.vue'
import mocAccomplishment from './MocAccomplishment.vue'

export default {
  name: 'emoc-common-workflow',
  props: ['emoc',
    'emocEditEnable',
    'checkRequiredBudgetType',
    'checkRequiredMocType',
    'checkRequiredChangeType',
    'checkRequiredCategoryType',
    'checkRequiredRiskAnalysis',
    'checkRequiredMocAccomplishment',
    'checkRequiredMechanicalChange',
    'checkRequiredInstrumentChange',
    'checkRequiredElectricalChange',
    'checkRequiredCivilChange',
    'checkRequiredIdledRetired',
    'validateSaveCommonWorkflow',
    'saveEmoc'
  ],
  data () {
    return {
      items: []
    }
  },
  mounted () {
    if (this.statusTechnicalReview) {
      this.closeTabByStatus()
    }
    if (this.statusHazardScreening) {
      this.closeTabByStatusHazardScreening()
    }
    if (this.statusImplementation) {
      this.closeTabByStatusImplementation()
    }
    if (this.statusReadinessReview) {
      this.closeTabByStatusReadinessReview()
    }
    if (this.statusCloseOut) {
      this.closeTabByStatusCloseOut()
    }
    if (this.statusClosed || this.statusLessonLearn) {
      this.closeTabByStatusClosed()
    }
  },
  computed: {
    showSectionMocFocalPoint () {
      return ['E-MOC Register', 'Emergency Approval'].indexOf(this.emoc.Status_Common_Workflow__c) === -1 && this.emoc.Status_Common_Workflow__c !== '' && this.emoc.show_MOC_Focal_Point_section__c
    },
    showHazardChecklist () {
      return ['', 'E-MOC Register', 'Emergency Approval', 'Concept Approval', 'Budget Registration', 'Detailed Design/Study'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    disabledNextStaEmocRegis () {
      return ['', 'E-MOC Register', 'Emergency Approval'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    disabledNextStaEmocRegis2 () {
      return ['', 'E-MOC Register', 'Emergency Approval', 'Concept Approval'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    disabledChangeType () {
      return ['', 'E-MOC Register', 'Emergency Approval', 'Hazard Screening', 'Technical Review', 'Implementation'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    disabledRamRatingInitial () {
      return ['', 'E-MOC Register', 'Emergency Approval', 'Concept Approval', 'Budget Registration', 'Detailed Design/Study', 'Hazard Screening'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    disabledRamRating () {
      return ['Hazard Screening'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    disabledRiskAnalysis () {
      return ['Hazard Screening'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    disabledTa () {
      return ['Hazard Screening', 'Technical Review'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    disabledTechnicalReviewMoc () {
      return ['Hazard Screening', 'Technical Review', 'Implementation'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    disabledPssrCategory () {
      return ['Readiness Review'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    disabledMocAccomplishment () {
      return ['Close Out'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    showTechnicalReview () {
      return this.showHazardChecklist && this.emoc.Status_Common_Workflow__c !== 'Hazard Screening'
    },
    showNotification () {
      return this.showTechnicalReview && ['Technical Review', 'APU Approval', 'VP Approval'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    showPssrCategory () {
      return this.showNotification && this.emoc.Status_Common_Workflow__c !== 'Implementation'
    },
    showMocAccomplishment () {
      return ['Close Out', 'Closed', 'Lesson Learn'].indexOf(this.emoc.Status_Common_Workflow__c) !== -1 && this.emoc.Send_to_MROx__c
    },
    showLessonLearned () {
      // return this.showPssrCategory && this.emoc.Status_Common_Workflow__c !== 'Readiness Review'
      return ['Closed', 'Lesson Learn'].indexOf(this.emoc.Status_Common_Workflow__c) !== -1 && this.emoc.Show_Lesson_Learn_section__c
    },
    statusHazardScreening () {
      return this.emoc.Status_Common_Workflow__c === 'Hazard Screening'
    },
    statusTechnicalReview () {
      return this.emoc.Status_Common_Workflow__c === 'Technical Review'
    },
    statusImplementation () {
      return this.emoc.Status_Common_Workflow__c === 'Implementation'
    },
    statusReadinessReview () {
      return this.emoc.Status_Common_Workflow__c === 'Readiness Review'
    },
    statusCloseOut () {
      return this.emoc.Status_Common_Workflow__c === 'Close Out'
    },
    statusClosed () {
      return this.emoc.Status_Common_Workflow__c === 'Closed'
    },
    statusLessonLearn () {
      return this.emoc.Status_Common_Workflow__c === 'Lesson Learn'
    }
  },
  methods: {
    onAdd () {
      this.items.push('Lorem ipsum dolor sit amet.')
    },
    closeTabByStatus () {
      if (this.$refs.emocTitleCmp.$refs.expandEmocTitle.isOpen) {
        this.$refs.emocTitleCmp.$refs.expandEmocTitle.toggle()
      }
      if (this.$refs.budgetTypeCmp.$refs.expandBudgetType.isOpen) {
        this.$refs.budgetTypeCmp.$refs.expandBudgetType.toggle()
      }
      if (this.$refs.functionalLocationCmp.$refs.expandFunctionalLocation.isOpen) {
        this.$refs.functionalLocationCmp.$refs.expandFunctionalLocation.toggle()
      }
      if (this.$refs.categoryTypeCmp.$refs.expandCategoryType.isOpen) {
        this.$refs.categoryTypeCmp.$refs.expandCategoryType.toggle()
      }
      if (this.$refs.mocTypeCmp.$refs.expandMOCType.isOpen) {
        this.$refs.mocTypeCmp.$refs.expandMOCType.toggle()
      }
      if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
        this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
      }
      if (this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.isOpen) {
        this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.toggle()
      }
      if (this.$refs.ramRatingCmp.$refs.expandRamRating.isOpen) {
        this.$refs.ramRatingCmp.$refs.expandRamRating.toggle()
      }
      if (this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.isOpen) {
        this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.toggle()
      }
      // if (this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.isOpen) {
      //   this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.toggle()
      // }
      // if (this.showHazardChecklist && this.emoc.Show_TA_Mandatory__c) {
      //   this.$nextTick(() => {
      //     if (this.$refs.ta2Cmp.$refs.expandTa2.isOpen) {
      //       this.$refs.ta2Cmp.$refs.expandTa2.toggle()
      //     }
      //   })
      // }
    },
    closeTabByStatusHazardScreening () {
      if (this.$refs.emocTitleCmp.$refs.expandEmocTitle.isOpen) {
        this.$refs.emocTitleCmp.$refs.expandEmocTitle.toggle()
      }
      if (this.$refs.budgetTypeCmp.$refs.expandBudgetType.isOpen) {
        this.$refs.budgetTypeCmp.$refs.expandBudgetType.toggle()
      }
      if (this.$refs.functionalLocationCmp.$refs.expandFunctionalLocation.isOpen) {
        this.$refs.functionalLocationCmp.$refs.expandFunctionalLocation.toggle()
      }
      if (this.$refs.functionalLocationCmpAssociate.$refs.expandFunctionLocationAssociate.isOpen) {
        this.$refs.functionalLocationCmpAssociate.$refs.expandFunctionLocationAssociate.toggle()
      }
      if (this.$refs.categoryTypeCmp.$refs.expandCategoryType.isOpen) {
        this.$refs.categoryTypeCmp.$refs.expandCategoryType.toggle()
      }
      if (this.$refs.mocTypeCmp.$refs.expandMOCType.isOpen) {
        this.$refs.mocTypeCmp.$refs.expandMOCType.toggle()
      }
      if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
        this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
      }
      if (this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.isOpen) {
        this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.toggle()
      }
      if (this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.isOpen) {
        this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.toggle()
      }
    },
    closeTabByStatusImplementation () {
      if (this.$refs.emocTitleCmp.$refs.expandEmocTitle.isOpen) {
        this.$refs.emocTitleCmp.$refs.expandEmocTitle.toggle()
      }
      if (this.$refs.functionalLocationCmpAssociate.$refs.expandFunctionLocationAssociate.isOpen) {
        this.$refs.functionalLocationCmpAssociate.$refs.expandFunctionLocationAssociate.toggle()
      }
      if (this.$refs.categoryTypeCmp.$refs.expandCategoryType.isOpen) {
        this.$refs.categoryTypeCmp.$refs.expandCategoryType.toggle()
      }
      if (this.$refs.mocTypeCmp.$refs.expandMOCType.isOpen) {
        this.$refs.mocTypeCmp.$refs.expandMOCType.toggle()
      }
      if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
        this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
      }
      if (this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.isOpen) {
        this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.toggle()
      }
      if (this.$refs.ramRatingCmp.$refs.expandRamRating.isOpen) {
        this.$refs.ramRatingCmp.$refs.expandRamRating.toggle()
      }
      if (this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.isOpen) {
        this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.toggle()
      }
      if (this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.isOpen) {
        this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.toggle()
      }
      if (this.showHazardChecklist && this.emoc.Show_TA_Mandatory__c) {
        this.$nextTick(() => {
          if (this.$refs.ta2Cmp.$refs.expandTa2.isOpen) {
            this.$refs.ta2Cmp.$refs.expandTa2.toggle()
          }
        })
      }
      if (this.showTechnicalReview) {
        this.$nextTick(() => {
          if (this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.isOpen) {
            this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.toggle()
          }
        })
      }
    },
    closeTabByStatusReadinessReview () {
      if (this.$refs.emocTitleCmp.$refs.expandEmocTitle.isOpen) {
        this.$refs.emocTitleCmp.$refs.expandEmocTitle.toggle()
      }
      if (this.$refs.budgetTypeCmp.$refs.expandBudgetType.isOpen) {
        this.$refs.budgetTypeCmp.$refs.expandBudgetType.toggle()
      }
      if (this.$refs.functionalLocationCmpAssociate.$refs.expandFunctionLocationAssociate.isOpen) {
        this.$refs.functionalLocationCmpAssociate.$refs.expandFunctionLocationAssociate.toggle()
      }
      if (this.$refs.categoryTypeCmp.$refs.expandCategoryType.isOpen) {
        this.$refs.categoryTypeCmp.$refs.expandCategoryType.toggle()
      }
      if (this.$refs.mocTypeCmp.$refs.expandMOCType.isOpen) {
        this.$refs.mocTypeCmp.$refs.expandMOCType.toggle()
      }
      if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
        this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
      }
      if (this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.isOpen) {
        this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.toggle()
      }
      if (this.$refs.ramRatingCmp.$refs.expandRamRating.isOpen) {
        this.$refs.ramRatingCmp.$refs.expandRamRating.toggle()
      }
      if (this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.isOpen) {
        this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.toggle()
      }
      if (this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.isOpen) {
        this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.toggle()
      }
      if (this.showHazardChecklist && this.emoc.Show_TA_Mandatory__c) {
        this.$nextTick(() => {
          if (this.$refs.ta2Cmp.$refs.expandTa2.isOpen) {
            this.$refs.ta2Cmp.$refs.expandTa2.toggle()
          }
        })
      }
      if (this.showTechnicalReview) {
        this.$nextTick(() => {
          if (this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.isOpen) {
            this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.toggle()
          }
        })
      }
      if (this.showTechnicalReview && this.emoc.Show_Technical_review_by_MOC__c) {
        this.$nextTick(() => {
          if (this.$refs.technicalReviewMocCmp.$refs.expandTaReviewMocFocal.isOpen) {
            this.$refs.technicalReviewMocCmp.$refs.expandTaReviewMocFocal.toggle()
          }
        })
      }
    },
    closeTabByStatusCloseOut () {
      if (this.$refs.emocTitleCmp.$refs.expandEmocTitle.isOpen) {
        this.$refs.emocTitleCmp.$refs.expandEmocTitle.toggle()
      }
      if (this.$refs.budgetTypeCmp.$refs.expandBudgetType.isOpen) {
        this.$refs.budgetTypeCmp.$refs.expandBudgetType.toggle()
      }
      if (this.$refs.functionalLocationCmpAssociate.$refs.expandFunctionLocationAssociate.isOpen) {
        this.$refs.functionalLocationCmpAssociate.$refs.expandFunctionLocationAssociate.toggle()
      }
      if (this.$refs.categoryTypeCmp.$refs.expandCategoryType.isOpen) {
        this.$refs.categoryTypeCmp.$refs.expandCategoryType.toggle()
      }
      if (this.$refs.mocTypeCmp.$refs.expandMOCType.isOpen) {
        this.$refs.mocTypeCmp.$refs.expandMOCType.toggle()
      }
      if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
        this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
      }
      if (this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.isOpen) {
        this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.toggle()
      }
      if (this.$refs.ramRatingCmp.$refs.expandRamRating.isOpen) {
        this.$refs.ramRatingCmp.$refs.expandRamRating.toggle()
      }
      if (this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.isOpen) {
        this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.toggle()
      }
      if (this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.isOpen) {
        this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.toggle()
      }
      if (this.showHazardChecklist && this.emoc.Show_TA_Mandatory__c) {
        this.$nextTick(() => {
          if (this.$refs.ta2Cmp.$refs.expandTa2.isOpen) {
            this.$refs.ta2Cmp.$refs.expandTa2.toggle()
          }
        })
      }
      if (this.showTechnicalReview) {
        this.$nextTick(() => {
          if (this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.isOpen) {
            this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.toggle()
          }
        })
      }
      if (this.showTechnicalReview && this.emoc.Show_Technical_review_by_MOC__c) {
        this.$nextTick(() => {
          if (this.$refs.technicalReviewMocCmp.$refs.expandTaReviewMocFocal.isOpen) {
            this.$refs.technicalReviewMocCmp.$refs.expandTaReviewMocFocal.toggle()
          }
        })
      }
      if (this.showPssrCategory) {
        this.$nextTick(() => {
          if (this.$refs.pssrCategoryCmp.$refs.expandPssrCategory.isOpen) {
            this.$refs.pssrCategoryCmp.$refs.expandPssrCategory.toggle()
          }
        })
      }
    },
    closeTabByStatusClosed () {
      if (this.$refs.emocTitleCmp.$refs.expandEmocTitle.isOpen) {
        this.$refs.emocTitleCmp.$refs.expandEmocTitle.toggle()
      }
      if (this.$refs.budgetTypeCmp.$refs.expandBudgetType.isOpen) {
        this.$refs.budgetTypeCmp.$refs.expandBudgetType.toggle()
      }
      if (this.$refs.functionalLocationCmpAssociate.$refs.expandFunctionLocationAssociate.isOpen) {
        this.$refs.functionalLocationCmpAssociate.$refs.expandFunctionLocationAssociate.toggle()
      }
      if (this.$refs.categoryTypeCmp.$refs.expandCategoryType.isOpen) {
        this.$refs.categoryTypeCmp.$refs.expandCategoryType.toggle()
      }
      if (this.$refs.mocTypeCmp.$refs.expandMOCType.isOpen) {
        this.$refs.mocTypeCmp.$refs.expandMOCType.toggle()
      }
      if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
        this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
      }
      if (this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.isOpen) {
        this.$refs.inputChangeDetailCmp.$refs.expandInputChangeDetail.toggle()
      }
      if (this.$refs.ramRatingCmp.$refs.expandRamRating.isOpen) {
        this.$refs.ramRatingCmp.$refs.expandRamRating.toggle()
      }
      if (this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.isOpen) {
        this.$refs.mocFocalPointCmp.$refs.expandMocFocalPoint.toggle()
      }
      if (this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.isOpen) {
        this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.toggle()
      }
      if (this.showHazardChecklist && this.emoc.Show_TA_Mandatory__c) {
        this.$nextTick(() => {
          if (this.$refs.ta2Cmp.$refs.expandTa2.isOpen) {
            this.$refs.ta2Cmp.$refs.expandTa2.toggle()
          }
        })
      }
      if (this.showTechnicalReview) {
        this.$nextTick(() => {
          if (this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.isOpen) {
            this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.toggle()
          }
        })
      }
      if (this.showTechnicalReview && this.emoc.Show_Technical_review_by_MOC__c) {
        this.$nextTick(() => {
          if (this.$refs.technicalReviewMocCmp.$refs.expandTaReviewMocFocal.isOpen) {
            this.$refs.technicalReviewMocCmp.$refs.expandTaReviewMocFocal.toggle()
          }
        })
      }
      if (this.showPssrCategory) {
        this.$nextTick(() => {
          if (this.$refs.pssrCategoryCmp.$refs.expandPssrCategory.isOpen) {
            this.$refs.pssrCategoryCmp.$refs.expandPssrCategory.toggle()
          }
        })
      }
      if (this.showMocAccomplishment) {
        this.$nextTick(() => {
          if (this.$refs.mocAccomplishmentCmp.$refs.expandMocAccomplishment.isOpen) {
            this.$refs.mocAccomplishmentCmp.$refs.expandMocAccomplishment.toggle()
          }
        })
      }
    }
  },
  components: {
    emocTitle,
    budgetType,
    functionalLocation,
    functionalLocationAssociate,
    categoryType,
    mocType,
    changeType,
    inputChangeDetail,
    ramRating,
    mocFocalPoint,
    riskAnalysis,
    ta2,
    technicalReview,
    taReviewMocFocal,
    notification,
    pssrCategory,
    mocAccomplishment,
    lessonLearned
  }
}
</script>


