<template>
  <div name="moc-focal-point">
    <expandable-section-std title="MOC Focal Point" ref="expandMocFocalPoint" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-md-6">
            <lookups-std v-model="emoc.MOC_Focal_Point_Name__c"
                         v-validate="showSectionMocFocalPoint ? 'required' : ''"
                         data-vv-as="MOC Focal Point Name"
                         data-vv-value-path="inputValue"
                         label="MOC Focal Point Name"
                         placeholder="Search..."
                         :options="optionsLookupFocalPoint"
                         :required="true"
                         :error="errors.has('MOC_Focal_Point_Name__c')"
                         name="MOC_Focal_Point_Name__c"
                         @search="filterLookupFocalPoint(arguments[0])"
                         icon="people"
                         loading
                         :disabled="mocFocalPointEditDisabled">
              <template #error>
                <span>{{ errors.first('MOC_Focal_Point_Name__c') }}</span>
              </template>
            </lookups-std>
          </div>
          <div class="col-md-6">
            <input-text-std v-model="emoc.MOC_Focal_Point_ID__c" label="MOC Focal Point ID" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <input-text-std v-model="emoc.Position__c" label="Position" disabled></input-text-std>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'moc-focal-point',
  props: [
    'emoc', 'showSectionMocFocalPoint'
  ],
  data () {
    return {}
  },
  created () {
    if (this.emoc.MOC_Focal_Point_Name__c !== '' && this.emoc.MOC_Focal_Point_Name__c) {
      this.filterLookupFocalPoint(this.emoc.MOC_Focal_Point_Name__c)
    } else {
      this.filterLookupFocalPoint('')
    }
  },
  computed: {
    lookupFocalPoint () { return this.$store.state.lookupFocalPoint['moc_focal_point'] },
    optionsLookupFocalPoint () {
      return this.lookupFocalPoint.map(dm => {
        return {
          value: dm.Id,
          label: dm.FirstName + ' ' + dm.LastName,
          meta: dm.Employee_Id__c,
          icon: 'people'
        }
      })
    },
    mocFocalPointEditDisabled () {
      return this.emoc.Status_Common_Workflow__c !== 'Concept Approval'
    }
  },
  watch: {
    'emoc.MOC_Focal_Point_Name__c' (newVal) {
      if (newVal !== '' && newVal) {
        let defaultFomula = this.lookupFocalPoint.filter(df => {
          return df.Id === newVal
        })
        if (defaultFomula.length !== 0) {
          this.emoc.Position__c = defaultFomula[0].INDICATOR__c
          this.emoc.MOC_Focal_Point_ID__c = defaultFomula[0].Employee_Id__c
        }
      } else {
        this.emoc.Position__c = ''
        this.emoc.MOC_Focal_Point_ID__c = ''
        this.filterLookupFocalPoint('')
      }
    }
  },
  methods: {
    filterLookupFocalPoint (searchParam) {
      this.$store.dispatch('receiveLookupFocalPoint', {lookupType: 'moc_focal_point', sendParam: searchParam})
    }
  }
}
</script>


