<template>
  <div name="modal-warning" class="modal-warning">
    <modal-std :id="id"
               size="small"
               :btnCloseDisp="false"
               @close="$emit('close')"
               ref="modalModalWarning">
      <template #content>
        <div class="row">
          <div class="col-3 icon-warning-block">
            <i class="fa fa-exclamation-triangle icon-warning" aria-hidden="true"></i>
          </div>
          <div class="col-9 padding-t5">
            <span class="message-warning">Please complete monitoring program requirement in Temporary tab.</span><br>
            <!-- <span class="message-warning-extend">Please Complete Monitoring Program Requirement</span> -->
          </div>
        </div>
      </template>
      <template #footer>
        <button :id="'btn' + id" class="slds-button slds-button_neutral" @click="onClose">Close</button>
      </template>
    </modal-std>
  </div>
</template>
<script>
export default {
  name: 'modal-warning',
  props: {
    id: String
  },
  methods: {
    onOpen () {
      this.$refs.modalModalWarning.onOpen()
    },
    onClose () {
      this.$refs.modalModalWarning.onClose()
    }
  }
}
</script>

<style scoped>
  .icon-warning-block {
    text-align: right;
  }
  .icon-warning {
    padding-right: 25px;
    font-size: 72px;
    color: #eb961f;
    vertical-align: middle;
  }
  .message-warning {
    font-size: 18px;
  }
  .message-warning-extend {
    font-size: 18px;
    opacity: 0.5;
  }
  .padding-t5 {
    padding-top: 6%;
  }
</style>



