<template>
  <div name="budget-type">
    <expandable-section-std title="Budget Type" :closedOnStart="false" ref="expandBudgetType">
      <div v-if="showBudgetEstimateInitial" class="data-frame">
        <div class="row">
          <div class="col-md-6">
            <checkbox-std
                v-model="emoc.Type_A_Maintenance_budget__c"
                label="Type A (Maintenance budget)"
                name="Type_A_Maintenance_budget__c"
                :inline="true"
                class="input-group"
                :disabled="budgetTypeEditDisabled"
            />
            <checkbox-std
                v-model="emoc.Type_B_Operation_budget__c"
                label="Type B (Operation budget)"
                :inline="true"
                class="input-group"
                :disabled="budgetTypeEditDisabled"
            />
            <checkbox-std
                v-model="emoc.Type_M_Maintenance_project__c"
                label="Type M (Maintenance project budget)"
                :inline="true"
                class="input-group"
                :disabled="budgetTypeEditDisabled"
            />
            <checkbox-std
                v-model="emoc.Type_P_Plant_change_budget__c"
                label="Type P (Plant change budget)"
                :inline="true"
                class="input-group"
                :disabled="budgetTypeEditDisabled"
            />
            <checkbox-std
                v-model="emoc.Type_S_Shutdown_budget__c"
                label="Type R (Shutdown budget)"
                :inline="true"
                class="input-group"
                :disabled="budgetTypeEditDisabled"
            />
            <checkbox-std
                v-model="emoc.Type_V_Un_plan_shutdown_budget__c"
                label="Type V (Un-plan shutdown budget)"
                :inline="true"
                class="input-group"
                :disabled="budgetTypeEditDisabled"
            />
          </div>
          <div class="col-md-6">
            <input-currency-std v-if="showBudgetEstimateInitial" 
                            v-model="emoc.Budget_Estimate_Initial__c"
                            v-validate="'required'"
                            data-vv-as="Budget Estimate Initial"
                            data-vv-value-path="inputValue"
                            name="Budget_Estimate_Initial__c"
                            label="Budget Estimate Initial"
                            addonPost="THB"
                            class="input-group"
                            :error="errors.has('Budget_Estimate_Initial__c')"
                            :required="true"
                            :disabled="emoc.Status_Common_Workflow__c !== 'Budget Registration'">
                            <!-- :helpText="'Minimum value of budget is 0.01. If no budget required please fill 0.01'" -->
              <template #error>
                <span>{{ errors.first('Budget_Estimate_Initial__c') }}</span>
              </template>
            </input-currency-std>
            <input-currency-std v-if="showBudgetEstimateFinal"
                            v-model="emoc.Budget_Estimate_Final__c"
                            v-validate="'required'"
                            data-vv-as="Budget Estimate Final"
                            data-vv-value-path="inputValue"
                            name="Budget_Estimate_Final__c"
                            label="Budget Estimate Final"
                            addonPost="THB"
                            class="input-group"
                            :error="errors.has('Budget_Estimate_Final__c')"
                            :required="true"
                            :disabled="budgetEstimateFinalDisabled">
                            <!-- :helpText="'Minimum value of budget is 0.01. If no budget required please fill 0.01'" -->
              <template #error>
                <span>{{ errors.first('Budget_Estimate_Final__c') }}</span>
              </template>
            </input-currency-std>
            <template v-if="showNotification">
              <label class="slds-form-element__label">Budget Number (WBS element)</label>
              <div class="slds-form-element__icon help-text-icon">
                <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
                  <i class="fa fa-info-circle" aria-hidden="true"></i>
                  <span class="slds-assistive-text">Help</span>
                </button>
                <div class="slds-popover slds-popover_tooltip  slds-nubbin_left help-text-display" role="tooltip" id="help" style="position:absolute;top:-66px;left:25px;">
                  <div class="slds-popover__body">
                    <div class="scrollbar-table">
                      <table id="budget-type-help-text" >
                        <tr>
                          <td>A-CC00000-DD-UUUUU</td>
                          <td>C= Company</td>
                        </tr>
                        <tr>
                          <td>M-CCYYNNN-DD-UUUUU</td>
                          <td>Y = Originating Year</td>
                        </tr>
                        <tr>
                          <td>P-CCYYNNN-DD-UUUUU</td>
                          <td>N = Running No</td>
                        </tr>
                        <tr>
                          <td>R-CCYYNNN-DD-UUUUU</td>
                          <td>D = Discipline</td>
                        </tr>
                        <tr>
                          <td>V-CC00000-DD-UUUUU</td>
                          <td>U = Unit</td>
                        </tr>
                        <tr>
                          <td>B-CC00000-DD-UUUUU</td>
                          <td></td>
                        </tr>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
              <input-text-std v-model="emoc.wbs_element__c"
                              v-validate="{ regex: flgValidateBudgetNumberFormat ? /.{1}-.{7}-.{2}-.{5}$/ : '' }"
                              data-vv-as="Budget Number (WBS element)"
                              data-vv-value-path="inputValue"
                              label=""
                              class="input-group padding-t0"
                              @focusout="focusoutWbs"
                              @focusin="focusinWbs"
                              :maxlength="15"
                              :error="errors.has('wbs_element__c')"
                              name="wbs_element__c"
                              :disabled="budgetWBSDisabled"
              >        
                <template #error>
                  <span>{{ errors.first('wbs_element__c') }}</span>
                </template>
              </input-text-std>
            </template>
          </div>
        </div>
      </div>
      <div v-else class="data-frame">
        <div class="row">
          <div class="col-md-6">
            <checkbox-std
                v-model="emoc.Type_A_Maintenance_budget__c"
                label="Type A (Maintenance budget)"
                name="Type_A_Maintenance_budget__c"
                :inline="true"
            />
          </div>
          <div class="col-md-6">
            <checkbox-std
                v-model="emoc.Type_B_Operation_budget__c"
                label="Type B (Operation budget)"
                :inline="true"
            />
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <checkbox-std
                v-model="emoc.Type_M_Maintenance_project__c"
                label="Type M (Maintenance project budget)"
                :inline="true"
            />
          </div>
          <div class="col-md-6">
            <checkbox-std
                v-model="emoc.Type_P_Plant_change_budget__c"
                label="Type P (Plant change budget)"
                :inline="true"
            />
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <checkbox-std
                v-model="emoc.Type_S_Shutdown_budget__c"
                label="Type R (Shutdown budget)"
                :inline="true"
            />
          </div>
          <div class="col-md-6">
            <checkbox-std
                v-model="emoc.Type_V_Un_plan_shutdown_budget__c"
                label="Type V (Un-plan shutdown budget)"
                :inline="true"
            />
          </div>
        </div>
      </div>
      <div v-show="checkRequiredBudgetType" class="slds-form-element slds-has-error">
        <div class="slds-form-element__help" id="error-unique-id-29">
          {{'Budget Type is required'}}
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'budget-type',
  props: [
    'emoc',
    'checkRequiredBudgetType',
    'showNotification'
  ],
  data () {
    return {
      flgValidateBudgetNumberFormat: true
    }
  },
  computed: {
    showBudgetEstimateInitial () {
      return ['', 'E-MOC Register', 'Emergency Approval', 'Concept Approval'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    showBudgetEstimateFinal () {
      return this.emoc.Status_Common_Workflow__c !== 'Budget Registration' && this.showBudgetEstimateInitial
    },
    budgetTypeEditDisabled () {
      return ['', 'E-MOC Register', 'Emergency Approval', 'Concept Approval', 'Budget Registration'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    budgetEstimateFinalDisabled () {
      return this.budgetTypeEditDisabled && ['Detailed Design/Study'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    budgetWBSDisabled () {
      return this.emoc.Status_Common_Workflow__c !== 'Implementation'
    }
  },
  watch: {
    'emoc.Type_A_Maintenance_budget__c' (newValue) {
      if (newValue) {
        this.emoc.Type_B_Operation_budget__c = false
        this.emoc.Type_M_Maintenance_project__c = false
        this.emoc.Type_P_Plant_change_budget__c = false
        this.emoc.Type_S_Shutdown_budget__c = false
        this.emoc.Type_V_Un_plan_shutdown_budget__c = false
        this.emoc.Budget_Type__c = 'Type A (Maintenance budget)'
      }
    },
    'emoc.Type_B_Operation_budget__c' (newValue) {
      if (newValue) {
        this.emoc.Type_A_Maintenance_budget__c = false
        this.emoc.Type_M_Maintenance_project__c = false
        this.emoc.Type_P_Plant_change_budget__c = false
        this.emoc.Type_S_Shutdown_budget__c = false
        this.emoc.Type_V_Un_plan_shutdown_budget__c = false
        this.emoc.Budget_Type__c = 'Type B (Operation budget)'
      }
    },
    'emoc.Type_M_Maintenance_project__c' (newValue) {
      if (newValue) {
        this.emoc.Type_A_Maintenance_budget__c = false
        this.emoc.Type_B_Operation_budget__c = false
        this.emoc.Type_P_Plant_change_budget__c = false
        this.emoc.Type_S_Shutdown_budget__c = false
        this.emoc.Type_V_Un_plan_shutdown_budget__c = false
        this.emoc.Budget_Type__c = 'Type M (Maintenance project budget)'
      }
    },
    'emoc.Type_P_Plant_change_budget__c' (newValue) {
      if (newValue) {
        this.emoc.Type_A_Maintenance_budget__c = false
        this.emoc.Type_B_Operation_budget__c = false
        this.emoc.Type_M_Maintenance_project__c = false
        this.emoc.Type_S_Shutdown_budget__c = false
        this.emoc.Type_V_Un_plan_shutdown_budget__c = false
        this.emoc.Budget_Type__c = 'Type P (Plant change budget)'
      }
    },
    'emoc.Type_S_Shutdown_budget__c' (newValue) {
      if (newValue) {
        this.emoc.Type_A_Maintenance_budget__c = false
        this.emoc.Type_B_Operation_budget__c = false
        this.emoc.Type_M_Maintenance_project__c = false
        this.emoc.Type_P_Plant_change_budget__c = false
        this.emoc.Type_V_Un_plan_shutdown_budget__c = false
        this.emoc.Budget_Type__c = 'Type R (Shutdown budget)'
      }
    },
    'emoc.Type_V_Un_plan_shutdown_budget__c' (newValue) {
      if (newValue) {
        this.emoc.Type_A_Maintenance_budget__c = false
        this.emoc.Type_B_Operation_budget__c = false
        this.emoc.Type_M_Maintenance_project__c = false
        this.emoc.Type_P_Plant_change_budget__c = false
        this.emoc.Type_S_Shutdown_budget__c = false
        this.emoc.Budget_Type__c = 'Type V (Un-plan shutdown budget)'
      }
    }
  },
  methods: {
    focusoutWbs () {
      this.flgValidateBudgetNumberFormat = true
      this.emoc.wbs_element__c = this.generateFormatWbs()
    },
    focusinWbs () {
      this.flgValidateBudgetNumberFormat = false
      this.emoc.wbs_element__c = this.clearFormatWbs()
    },
    generateFormatWbs () {
      // return this.clearFormatWbs().replace(/\B(?=(\d{3})+(?!\d))/g, '-')
      return this.clearFormatWbs().replace(/(.{1})(.{1,7})(.{1,2})(.{1,5})/g, '$1-$2-$3-$4')
    },
    clearFormatWbs () {
      return this.emoc.wbs_element__c.replace(/-/g, '')
    }
  }
}
</script>
<style scoped>
  #budget-type-help-text td, #budget-type-help-text th {
    border: 1px solid #ddd;
    padding: 5px;
  }
  .padding-t0 {
    padding-top: 0px;
  }
</style>



