<template>
  <div name="functional-override">
    <functional-location :emoc="emoc"
                         :emocEditEnable="emocEditEnable"
                         ref="functionalLocationCmp" />

    <!-- <notification           :emoc="emoc"
                            :emocEditEnable="emocEditEnable"
                            ref="notificationCmp" /> -->

    <group :emoc="emoc"
           :checkRequiredGroup="checkRequiredGroup"
           ref="group" />

    <change-type            :emoc="emoc"
                            :recordType="'Functional Override'"
                            :disabledNextStaEmocRegis="false"
                            :disabledChangeType="false"
                            ref="changeTypeCmp" />

    <prelim-risk-assessment :emoc="emoc"
                            ref="prelimRiskAssessmentCmp" />

    <ram-rating :emoc="emoc"
                :showHazardChecklist="showHazardChecklist"
                :disabledRamRating="disabledRamRating"
                ref="ramRatingCmp" />
  </div>
</template>
<script>
import functionalLocation from './FunctionalLocation.vue'
import notification from './NotificationFn.vue'
import group from './Group.vue'
import changeType from './ChangeType.vue'
import prelimRiskAssessment from './PrelimRiskAssessment.vue'
import ramRating from './RamRating.vue'

export default {
  name: 'functional-override',
  props: ['emoc', 'emocEditEnable', 'checkRequiredGroup'],
  data () {
    return {
      items: []
    }
  },
  computed: {
    showHazardChecklist () {
      return false
    },
    disabledRamRating () {
      return false
    }
  },
  methods: {
    onAdd () {
      this.items.push('Lorem ipsum dolor sit amet.')
    }
  },
  components: {
    functionalLocation,
    notification,
    group,
    ramRating,
    changeType,
    prelimRiskAssessment
  }
}
</script>


