<template>
  <div name="input-change-detail">
    <expandable-section-std title="Input Change Detail" ref="expandInputChangeDetail" :closedOnStart="false">
      <div class="data-frame">
        <textarea-std
          v-model="emoc.Description_of_change__c"
          v-validate="'required'"
          data-vv-as="Description of change"
          data-vv-value-path="inputValue"
          name="Description_of_change__c"
          label="Description of change"
          :error="errors.has('Description_of_change__c')"
          :required="true"
          :disabled="disabledNextStaEmocRegis"
        >
          <template #error>
            <span>{{ errors.first('Description_of_change__c') }}</span>
          </template>
        </textarea-std>
        <textarea-std
          v-model="emoc.Reason_of_change__c"
          v-validate="'required'"
          data-vv-as="Reason of change"
          data-vv-value-path="inputValue"
          name="Reason_of_change__c"
          label="Reason of change"
          :error="errors.has('Reason_of_change__c')"
          :required="true"
          :disabled="disabledNextStaEmocRegis"
        >
          <template #error>
            <span>{{ errors.first('Reason_of_change__c') }}</span>
          </template>
        </textarea-std>
        <textarea-std
          v-model="emoc.Justification__c"
          v-validate="'required'"
          data-vv-as="Justification"
          data-vv-value-path="inputValue"
          name="Justification__c"
          label="Justification"
          :error="errors.has('Justification__c')"
          :required="true"
          :disabled="disabledNextStaEmocRegis"
        >
          <template #error>
            <span>{{ errors.first('Justification__c') }}</span>
          </template>
        </textarea-std>
        <textarea-std
          v-model="emoc.Precaution__c"
          label="Precaution"
          :disabled="disabledNextStaEmocRegis"
        />
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'input-change-detail',
  props: [
    'emoc', 'disabledNextStaEmocRegis'
  ],
  data () {
    return {}
  }
}
</script>


