<template>
  <div class="create-emoc">
    <div class="slds-card__header slds-grid header custom-border-header">
      <header class="slds-media slds-media_center slds-has-flexi-truncate">
        <div class="slds-media__body" >
          <h2>
            <span class="slds-card__header-link slds-text-heading_small">{{emocEditEnable ? 'Edit E-MOC' : 'Create E-MOC'}}</span>
            <span v-show="!emocEditEnable" style="float: right;">{{numpage}} / 3 page</span>
          </h2>
        </div>
      </header>
    </div>
    <div v-show="numpage === 1">
      <div class="slds-card__body slds-card__body_inner">
        <checkbox-std
          v-model="emoc.OPM_OPEX__c"
          label="OPM (OPEX)"
          :inline="true"
          class="input-group"
        />
        <checkbox-std
          v-model="emoc.CPM_CAPEX__c"
          label="CPM (CAPEX)"
          :inline="true"
          class="input-group"
        />
        <div v-show="cpmCapexNextFalse" class="slds-form-element slds-has-error">
          <div class="slds-form-element__help" id="error-unique-id-29">
            {{'CAPEX Not allow to proceed e-MOC'}}
          </div>
        </div>
      </div>
    </div>
    <div v-show="numpage === 2">
      <div class="slds-card__body slds-card__body_inner">
        <checkbox-std
          v-model="emoc.Common_Workflow__c"
          label="Common MOC Workflow"
          :inline="true"
          class="input-group"
        />
        <checkbox-std
          v-model="emoc.Functional_Override__c"
          label="Functional Override"
          :inline="true"
          class="input-group"
        />
      </div>
    </div>
    <div v-show="numpage === 3">
      <modal-after-save @close="closePopupSuccess"
                        ref="popupAfterSave"/>

      <modal-warning    @close="closePopupWarnings"
                        ref="popupWarning"/>

      <common-workflow v-if="emoc.Common_Workflow__c"
                       :emoc="emoc"
                       :emocEditEnable="emocEditEnable"
                       :checkRequiredBudgetType="checkRequiredBudgetType"
                       :checkRequiredMocType="checkRequiredMocType"
                       :checkRequiredChangeType="checkRequiredChangeType"
                       :checkRequiredCategoryType="checkRequiredCategoryType"
                       :checkRequiredRiskAnalysis="checkRequiredRiskAnalysis"
                       :validateSaveCommonWorkflow="validateSaveCommonWorkflow"
                       :checkRequiredMocAccomplishment="checkRequiredMocAccomplishment"
                       :checkRequiredMechanicalChange="checkRequiredMechanicalChange"
                       :checkRequiredInstrumentChange="checkRequiredInstrumentChange"
                       :checkRequiredElectricalChange="checkRequiredElectricalChange"
                       :checkRequiredCivilChange="checkRequiredCivilChange"
                       :checkRequiredIdledRetired="checkRequiredIdledRetired"
                       :saveEmoc="saveEmoc"
                       ref="commonWorkflow"/>
      <functional-override v-if="emoc.Functional_Override__c"
                           :emocEditEnable="emocEditEnable"
                           :emoc="emoc"
                           :checkRequiredGroup="checkRequiredGroup"
                           ref="functionalOverride"/>
    </div>
    <footer class="slds-card__footer">
      <!-- <button class="slds-button slds-button_neutral" @click="clickCancel">Cancel</button> -->
      <div class="row">
        <div class="col-md-4"></div>
        <div class="col-md-4">
          <popover-error-std v-show="hasError && numpage === 3"
                             :errorBagItems="errorBagItems"
                             :getLabelFields="getLabelFields"
                             :focusField="focusField"
                             ref="popoverErrorStd"/>
        </div>
        <div class="col-md-4 btn-footer">
          <button v-show="numpage !== 1 && !emocEditEnable" class="slds-button slds-button_neutral" @click="clickPrevious">Previous</button>
          <button v-show="numpage !== 3 && !emocEditEnable" class="slds-button slds-button_brand" :disabled="!btnNextEnable" @click="clickNext">Next</button>
          <button v-show="numpage === 3" class="slds-button slds-button_brand" @click="saveEmoc">Save</button>
        </div>
      </div>
    </footer>
    <modal-std size="small" ref="refModalAlterMsg"
               @close="afterCloseModalAlterMsg"
               id="modalAlterMsgId">
      <!-- <template #header>
        <div style="text-align: left;"><span class="slds-card__header-link slds-text-heading_small">Alert</span></div>
      </template> -->
      <template #content>
        <span style="font-size: 18px;">Please click submit button for sending to technical review</span>
      </template>
      <template #footer>
        <div style="text-align: right;">
          <button class="slds-button slds-button_neutral" @click="btnOkAlert">OK</button>
        </div>
      </template>
    </modal-std>
    <modal-waiting ref="popupLoader"/>
  </div>
</template>
<script>
import commonWorkflow from './CommonWorkflow.vue'
import functionalOverride from './FunctionalOverride.vue'
export default {
  name: 'create-emoc',
  data () {
    return {
      numpage: 1,
      hasError: false,
      errorBagItems: [],
      recId: '',
      resultSuccess: {},
      cpmCapexNextFalse: false,
      indexOfRam: ['L/N', 'L', 'M', 'H']
    }
  },
  created () {
    this.$store.dispatch('receiveLookupFncLocation', { type: 'main', sendParam: '' })
    this.$store.dispatch('receiveLookupUser', { type: 'main', sendParam: '' })
    this.$store.dispatch('receiveLookupClearanceNumber', '')
    this.$store.dispatch('receivePotentialRiskList')
    this.$store.dispatch('receivePotentialConsequenceList')
    this.$store.dispatch('receiveMitigationPrecautionList')
  },
  mounted () {
    const queryString = window.location.search
    const urlParams = new URLSearchParams(queryString)
    this.recId = urlParams.get('id')
    let paramSearch = {
      emocApplicationId: this.recId
    }
    if (this.emocEditEnable) {
      this.$store.dispatch('receiveEmocApplication', paramSearch)
      this.$store.dispatch('receivePotentialConsequence', paramSearch)
      this.$store.dispatch('receiveMitigationPrecaution', paramSearch)
    }
  },
  computed: {
    emoc () { return this.$store.state.emoc },
    btnNextEnable () {
      let conditionPage = false
      if ((this.emoc.CPM_CAPEX__c || this.emoc.OPM_OPEX__c) && this.numpage === 1) {
        conditionPage = true
      } else if ((this.emoc.Common_Workflow__c || this.emoc.Functional_Override__c) && this.numpage === 2) {
        conditionPage = true
      } else {
        conditionPage = false
      }
      return conditionPage
    },
    emocEditEnable () {
      return this.recId && this.recId !== ''
    },
    checkRequiredBudgetType () {
      return !(this.emoc.Type_A_Maintenance_budget__c || this.emoc.Type_B_Operation_budget__c || this.emoc.Type_M_Maintenance_project__c || this.emoc.Type_P_Plant_change_budget__c || this.emoc.Type_S_Shutdown_budget__c || this.emoc.Type_V_Un_plan_shutdown_budget__c)
    },
    checkRequiredMocType () {
      return !(this.emoc.Normal__c || this.emoc.Emergency__c)
    },
    checkRequiredChangeType () {
      return !(this.emoc.Permanent__c || this.emoc.Temporary__c)
    },
    checkRequiredCategoryType () {
      return !(this.emoc.Mechanical_change__c ||
        this.emoc.Instrument_change__c ||
        this.emoc.Electrical_change__c ||
        this.emoc.Civil_change__c ||
        this.emoc.Catalyst_Chemical__c ||
        this.emoc.Design_limit_IOW_change__c ||
        this.emoc.Tank_change_service__c ||
        this.emoc.Idled_Retired_Assets_equipment__c
      )
    },
    checkRequiredMechanicalChange () {
      return (this.emoc.Mechanical_change__c && !(
        this.emoc.Air_Cooled_Exchangers__c ||
        this.emoc.Fired_Heaters_Fire_Boiler__c ||
        this.emoc.Pipes_Flanges_Valve__c ||
        this.emoc.Tracing_steam_Steam_trap__c ||
        this.emoc.Pressure_Vessels__c ||
        this.emoc.Rotating_equipment__c ||
        this.emoc.Relief_Systems__c ||
        this.emoc.Shell_Tube_Heat_Exchangers__c ||
        this.emoc.Tanks__c ||
        this.emoc.SBM_CBM__c ||
        this.emoc.HVAC_Refrigeration__c ||
        this.emoc.Loading_arm_Jetty_Lorry__c ||
        this.emoc.Hydraulic_power_unit__c ||
        this.emoc.Safety_Equipment__c ||
        this.emoc.Hot_tap__c ||
        this.emoc.Temporary_repair__c
      ))
    },
    checkRequiredInstrumentChange () {
      return (this.emoc.Instrument_change__c && !(
        this.emoc.Alarm__c ||
        this.emoc.APC__c ||
        this.emoc.Base_layer_control_DCS_PLC_TAS_TAM__c ||
        this.emoc.SIF__c ||
        this.emoc.Range__c ||
        this.emoc.Control_valve__c ||
        this.emoc.Instrumentation_equipment__c ||
        this.emoc.QMI__c ||
        this.emoc.Valve__c ||
        this.emoc.Fire_Gas_detection_system__c
      ))
    },
    checkRequiredElectricalChange () {
      return (this.emoc.Electrical_change__c && !(
        this.emoc.Electrical_equipment_system__c ||
        this.emoc.Emergency_Backup_Systems__c ||
        this.emoc.Communication__c ||
        this.emoc.Driver_Actuator_process_at_Valve__c
      ))
    },
    checkRequiredCivilChange () {
      return (this.emoc.Civil_change__c && !(
        this.emoc.Buildling_and_Road__c ||
        this.emoc.Structures_foundations__c ||
        this.emoc.Drainage_system_AOC_COC_etc_Bund__c ||
        this.emoc.Refractory_Fireproof__c ||
        this.emoc.Insulation__c ||
        this.emoc.Painting_and_coating__c
      ))
    },
    checkRequiredIdledRetired () {
      return (this.emoc.Idled_Retired_Assets_equipment__c && !(
        this.emoc.Demolition__c ||
        this.emoc.Mothballing__c ||
        this.emoc.Preservation_unit__c
      ))
    },
    checkRequiredRiskAnalysis () {
      return !(this.emoc.HAZOP__c || this.emoc.HRA__c || this.emoc.Environmental_Impact__c || this.emoc.Reliability_Checklist__c) &&
      this.emoc.Status_Common_Workflow__c === 'Hazard Screening' &&
      ['H', 'M'].indexOf(this.emoc.RAM_Total__c) !== -1
    },
    checkRequiredGroup () {
      return !(this.emoc.OOS__c ||
        this.emoc.MOS_for_ops_purpose__c ||
        this.emoc.Inhibit_for_process_or_Equipment_S_D__c ||
        this.emoc.Inhibit_alarm_for_maintenance__c ||
        this.emoc.Disable_Alarm_by_Ops_for_HW__c ||
        this.emoc.Inhibit_Alarm_request_for_FOX__c ||
        this.emoc.MOS_for_maintenance__c ||
        this.emoc.Safeguarding_force_signal__c
      )
    },
    showBudgetEstimateInitial () {
      return ['', 'E-MOC Register', 'Emergency Approval', 'Concept Approval'].indexOf(this.emoc.Status_Common_Workflow__c) === -1
    },
    showSectionTa2 () {
      return (this.emoc.Status_Common_Workflow__c === 'Hazard Screening') && this.emoc.Show_TA_Mandatory__c
    },
    showPssrCategory () {
      return this.$refs.commonWorkflow.showPssrCategory
    },
    showMocAccomplishment () {
      return this.$refs.commonWorkflow.showMocAccomplishment
    },
    showLessonLearned () {
      return this.$refs.commonWorkflow.showLessonLearned
    },
    taReviewbyTechnologistDisabled () { return this.$store.state.taReviewbyTechnologistDisabled },
    taReviewbyOperationDisabled () { return this.$store.state.taReviewbyOperationDisabled },
    taReviewbyEngineerDisabled () { return this.$store.state.taReviewbyEngineerDisabled },
    checkRamFinal () {
      if (this.emoc.RAM_Total__c && this.emoc.RAM_Total_Final__c) {
        return this.indexOfRam.indexOf(this.emoc.RAM_Total_Final__c) <= this.indexOfRam.indexOf(this.emoc.RAM_Total__c)
      } else {
        return true
      }
    },
    checkRamInitialH () {
      if (this.emoc.RAM_Total_Final__c && this.emoc.RAM_Total__c === 'H') {
        return this.emoc.RAM_Total_Final__c !== 'H'
      } else {
        return true
      }
    },
    checkRequiredMocAccomplishment () {
      return ['Close Out', 'Closed', 'Lesson Learn'].indexOf(this.emoc.Status_Common_Workflow__c) !== -1 && this.emoc.Send_to_MROx__c && !(this.emoc.Yes_MOC_Accomplishment__c || this.emoc.No_MOC_Accomplishment__c)
    }
  },
  watch: {
    'emoc.CPM_CAPEX__c' (newValue) {
      if (newValue) {
        this.emoc.OPM_OPEX__c = false
      }
    },
    'emoc.OPM_OPEX__c' (newValue) {
      if (newValue) {
        this.emoc.CPM_CAPEX__c = false
      }
    },
    'emoc.Common_Workflow__c' (newValue) {
      if (newValue) {
        this.emoc.Functional_Override__c = false
        let paramSearch = {
          searchParam: '',
          recordType: 'Common Workflow'
        }
        this.$store.dispatch('receiveLookupFncLocationFilter', { type: 'main', sendParam: paramSearch })
      }
    },
    'emoc.Functional_Override__c' (newValue) {
      if (newValue) {
        this.emoc.Common_Workflow__c = false
        let paramSearch = {
          searchParam: '',
          recordType: 'Functional Override'
        }
        this.$store.dispatch('receiveLookupFncLocationFilter', { type: 'main', sendParam: paramSearch })
      }
    },
    recId (newVal) {
      if (newVal !== '' && newVal) {
        this.numpage = 3
      }
    }
  },
  methods: {
    showPopup () {
      this.$refs.popupAfterSave.onOpen()
    },
    showPopupWarning () {
      this.$refs.popupWarning.onOpen()
    },
    afterCloseModalAlterMsg () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    clickNext () {
      this.cpmCapexNextFalse = false
      if (this.emoc.OPM_OPEX__c && this.numpage === 1) {
        this.numpage++
      } else if (this.emoc.CPM_CAPEX__c && this.numpage === 1) {
        this.cpmCapexNextFalse = true
      } else if ((this.emoc.Common_Workflow__c || this.emoc.Functional_Override__c) && this.numpage === 2) {
        this.numpage++
      }
    },
    clickPrevious () {
      this.numpage--
      this.hasError = false
      if (this.emoc.Functional_Override__c) {
        this.emoc.Temporary__c = false
      }
    },
    clickCancel () {
      this.numpage = 1
    },
    getLabelFields (field) {
      let listFieldsLabel = {
        Budget_Estimate_Initial__c: 'Budget Estimate Initial',
        Budget_Estimate_Final__c: 'Budget Estimate Final',
        Functional_Location__c: 'Functional Location',
        Completion_date__c: 'Required Completion date',
        Start_date__c: 'Required Start date',
        End_date__c: 'Required End date',
        Description_of_change__c: 'Description of change',
        Reason_of_change__c: 'Reason of change',
        Justification__c: 'Justification',
        Requestor_Name__c: 'Requestor Name',
        Type_A_Maintenance_budget__c: 'Budget Type',
        Normal__c: 'MOC Type',
        Permanent__c: 'Change Type',
        Assets_Severity__c: 'Assets Severity',
        Assets_Likelihood__c: 'Assets Likelihood',
        Environment_Severity__c: 'Environment Severity',
        Environment_Likelihood__c: 'Environment Likelihood',
        People_Severity__c: 'People Severity',
        People_Likelihood__c: 'People Likelihood',
        Product_Quality_Severity__c: 'Product Quality Severity',
        Product_Quality_Likelihood__c: 'Product Quality Likelihood',
        Reputation_Severity__c: 'Reputation Severity',
        Reputation_Likelihood__c: 'Reputation Likelihood',
        Assets_Severity_Final__c: 'Assets Severity',
        Assets_Likelihood_Final__c: 'Assets Likelihood',
        Environment_Severity_Final__c: 'Environment Severity',
        Environment_Likelihood_Final__c: 'Environment Likelihood',
        People_Severity_Final__c: 'People Severity',
        People_Likelihood_Final__c: 'People Likelihood',
        Product_Quality_Severity_Final__c: 'Product Quality Severity',
        Product_Quality_Likelihood_Final__c: 'Product Quality Likelihood',
        Reputation_Severity_Final__c: 'Reputation Severity',
        Reputation_Likelihood_Final__c: 'Reputation Likelihood',
        Mechanical_change__c: 'Category Type & Sub-Category Type',
        MOC_Focal_Point_Name__c: 'MOC Focal Point Name',
        HAZOP__c: 'Risk Analysis',
        Reason_of_Work__c: 'Reason of Work',
        Potential_Risk_c__c: 'Potential Risk',
        OOS__c: 'Functional override categories',
        Reason_MOC_Accomplishment__c: 'Reason',
        Lesson_Learn__c: 'Lesson Learned',
        Recommendation_for_future_improvement__c: 'Recommendation for future improvement',
        btnHelpTextRamFinal: !this.checkRamInitialH ? 'Final Risk cannot higher than Initial risk or H' : 'Final Risk cannot higher than Initial risk',
        Assigned_To_QA_QC__c: 'MOC Focal Point Name',
        Work_Clearance_c__c: 'Reference clearance certificate number',
        Yes_MOC_Accomplishment__c: 'MOC Accomplishment verification by MROx',
        E_MOC_Title__c: 'E-MOC Title',
        Air_Cooled_Exchangers__c: 'Sub Category Type in Mechanical change',
        Alarm__c: 'Sub Category Type in Instrument change',
        Electrical_equipment_system__c: 'Sub Category Type in Electrical change',
        Buildling_and_Road__c: 'Sub Category Type in Civil change',
        Demolition__c: 'Sub Category Type in Idled / Retired Assets (equipment)'
      }
      if (field.indexOf('TA2_Indicator') !== -1) {
        return 'Indicator / Name'
      } else if (field.indexOf('Potential_Consequence_c__c') !== -1) {
        return 'Potential Consequence'
      } else if (field.indexOf('Other_Potential_Consequence__c') !== -1) {
        return 'Additional Description of Potential consequence'
      } else if (field.indexOf('Mitigation_Precaution_1__c') !== -1) {
        return 'Mitigation/Precaution'
      } else if (field.indexOf('Other_Mitigation__c') !== -1) {
        return 'Additional Description of Mitigation/Precaution'
      } else if (field.indexOf('Asso_Functional_Location__c') !== -1) {
        return 'Functional Location'
      } else {
        return listFieldsLabel[field]
      }
    },
    focusField (target) {
      let elements = document.getElementsByName(target)
      if (this.emoc.Common_Workflow__c) {
        let refEmocTitle = this.$refs.commonWorkflow.$refs.emocTitleCmp
        let refFuctionLocation = this.$refs.commonWorkflow.$refs.functionalLocationCmp
        let refFuctionLocationAssociate = this.$refs.commonWorkflow.$refs.functionalLocationCmpAssociate
        let refChangeType = this.$refs.commonWorkflow.$refs.changeTypeCmp
        let refInputChangeDetail = this.$refs.commonWorkflow.$refs.inputChangeDetailCmp
        let refBudgetType = this.$refs.commonWorkflow.$refs.budgetTypeCmp
        let refMocType = this.$refs.commonWorkflow.$refs.mocTypeCmp
        let refRamRating = this.$refs.commonWorkflow.$refs.ramRatingCmp
        let categoryType = this.$refs.commonWorkflow.$refs.categoryTypeCmp
        let mocFocalPoint = this.$refs.commonWorkflow.$refs.mocFocalPointCmp
        let riskAnalysis = this.$refs.commonWorkflow.$refs.riskAnalysisCmp
        let ta2
        if (this.showSectionTa2) {
          ta2 = this.$refs.commonWorkflow.$refs.ta2Cmp
        }
        let mocAccomplishment
        if (this.showMocAccomplishment) {
          mocAccomplishment = this.$refs.commonWorkflow.$refs.mocAccomplishmentCmp
        }
        let pssrCategory
        if (this.showPssrCategory) {
          pssrCategory = this.$refs.commonWorkflow.$refs.pssrCategoryCmp
        }
        let lessonLearned
        if (this.showLessonLearned) {
          lessonLearned = this.$refs.commonWorkflow.$refs.lessonLearnedCmp
        }
        if (target === 'E_MOC_Title__c' && !refEmocTitle.$refs.expandEmocTitle.isOpen) {
          refEmocTitle.$refs.expandEmocTitle.toggle()
        } else if (target === 'Functional_Location__c' && !refFuctionLocation.$refs.expandFunctionalLocation.isOpen) {
          refFuctionLocation.$refs.expandFunctionalLocation.toggle()
        } else if (target.indexOf('Asso_Functional_Location__c') !== -1 && !refFuctionLocationAssociate.$refs.expandFunctionLocationAssociate.isOpen) {
          refFuctionLocationAssociate.$refs.expandFunctionLocationAssociate.toggle()
        } else if (['Completion_date__c', 'Start_date__c', 'End_date__c', 'Permanent__c'].indexOf(target) !== -1 && !refChangeType.$refs.expandChangeType.isOpen) {
          refChangeType.$refs.expandChangeType.toggle()
        } else if (['Description_of_change__c', 'Reason_of_change__c', 'Justification__c'].indexOf(target) !== -1 && !refInputChangeDetail.$refs.expandInputChangeDetail.isOpen) {
          refInputChangeDetail.$refs.expandInputChangeDetail.toggle()
        } else if (['Type_A_Maintenance_budget__c', 'Budget_Estimate_Initial__c', 'Budget_Estimate_Final__c'].indexOf(target) !== -1 && !refBudgetType.$refs.expandBudgetType.isOpen) {
          refBudgetType.$refs.expandBudgetType.toggle()
        } else if (target === 'Normal__c' && !refMocType.$refs.expandMOCType.isOpen) {
          refMocType.$refs.expandMOCType.toggle()
        } else if ([
          'Assets_Severity__c', 'Assets_Likelihood__c',
          'Environment_Severity__c', 'Environment_Likelihood__c',
          'People_Severity__c', 'People_Likelihood__c',
          'Product_Quality_Severity__c', 'Product_Quality_Likelihood__c',
          'Reputation_Severity__c', 'Reputation_Likelihood__c',
          'Assets_Severity_Final__c', 'Assets_Likelihood_Final__c',
          'Environment_Severity_Final__c', 'Environment_Likelihood_Final__c',
          'People_Severity_Final__c', 'People_Likelihood_Final__c',
          'Product_Quality_Severity_Final__c', 'Product_Quality_Likelihood_Final__c',
          'Reputation_Severity_Final__c', 'Reputation_Likelihood_Final__c',
          'btnHelpTextRamFinal'].indexOf(target) !== -1 && !refRamRating.$refs.expandRamRating.isOpen) {
          refRamRating.$refs.expandRamRating.toggle()
        } else if (target === 'Mechanical_change__c' && !categoryType.$refs.expandCategoryType.isOpen) {
          categoryType.$refs.expandCategoryType.toggle()
        } else if (target === 'MOC_Focal_Point_Name__c' && !mocFocalPoint.$refs.expandMocFocalPoint.isOpen) {
          mocFocalPoint.$refs.expandMocFocalPoint.toggle()
        } else if (target === 'HAZOP__c' && !riskAnalysis.$refs.expandRiskAnalysis.isOpen) {
          riskAnalysis.$refs.expandRiskAnalysis.toggle()
        } else if (target.indexOf('TA2_Indicator') !== -1 && !ta2.$refs.expandTa2.isOpen) {
          ta2.$refs.expandTa2.toggle()
        } else if (['Assigned_To_QA_QC__c'].indexOf(target) !== -1 && !pssrCategory.$refs.expandPssrCategory.isOpen) {
          pssrCategory.$refs.expandPssrCategory.toggle()
        } else if (['Reason_MOC_Accomplishment__c'].indexOf(target) !== -1 && !mocAccomplishment.$refs.expandMocAccomplishment.isOpen) {
          mocAccomplishment.$refs.expandMocAccomplishment.toggle()
        } else if (['Lesson_Learn__c', 'Recommendation_for_future_improvement__c'].indexOf(target) !== -1 && !lessonLearned.$refs.expandLessonLearn.isOpen) {
          lessonLearned.$refs.expandLessonLearn.toggle()
        }
      } else {
        let refFuctionLocation = this.$refs.functionalOverride.$refs.functionalLocationCmp
        let refChangeType = this.$refs.functionalOverride.$refs.changeTypeCmp
        let refPrelimRiskAssessment = this.$refs.functionalOverride.$refs.prelimRiskAssessmentCmp
        let refGroup = this.$refs.functionalOverride.$refs.group
        if (target === 'Functional_Location__c' && !refFuctionLocation.$refs.expandFunctionalLocation.isOpen) {
          refFuctionLocation.$refs.expandFunctionalLocation.toggle()
        } else if (['Start_date__c', 'End_date__c', 'Reason_of_Work__c'].indexOf(target) !== -1 && !refChangeType.$refs.expandChangeType.isOpen) {
          refChangeType.$refs.expandChangeType.toggle()
        } else if ([
          'Potential_Risk_c__c',
          'Other__c',
          'Potential_Consequence_c__c',
          'Other_Potential_Consequence__c',
          'Mitigation_Precaution_1__c',
