              // CATEGORY_2_Form__c: this.emoc.CATEGORY_2_Form__c,
              CATEGORY_3__c: this.emoc.CATEGORY_3__c,
              Assigned_To_Category_3__c: this.emoc.Assigned_To_Category_3__c,
              Employee_ID_Category_3__c: this.emoc.Employee_ID_Category_3__c,
              Position_Category_3__c: this.emoc.Position_Category_3__c,
              // CATEGORY_3_Form__c: this.emoc.CATEGORY_3_Form__c,
              QA_QC__c: this.emoc.QA_QC__c,
              Assigned_To_QA_QC__c: this.emoc.Assigned_To_QA_QC__c,
              Employee_ID_QA_QC__c: this.emoc.Employee_ID_QA_QC__c,
              Position_QA_QC__c: this.emoc.Position_QA_QC__c,
              Position_Lesson_Learned__c: this.emoc.Position_Lesson_Learned__c,
              Yes_MOC_Accomplishment__c: this.emoc.Yes_MOC_Accomplishment__c,
              No_MOC_Accomplishment__c: this.emoc.No_MOC_Accomplishment__c,
              Reason_MOC_Accomplishment__c: this.emoc.Reason_MOC_Accomplishment__c,
              Lesson_Learn__c: this.emoc.Lesson_Learn__c,
              Recommendation_for_future_improvement__c: this.emoc.Recommendation_for_future_improvement__c,
              Status_Common_Report__c: this.emoc.Status_Common_Report__c,
              Status_FN_Report__c: this.emoc.Status_FN_Report__c,
              Action_by_MOC__c: this.emoc.Action_by_MOC__c,
              I_have_confirmed_Risk_Analysis__c: this.emoc.I_have_confirmed_Risk_Analysis__c,
              Action_by_risk_analysis__c: this.emoc.Action_by_risk_analysis__c,
              TA2_s_review_has_been_completed__c: this.emoc.I_have_incorporated_comments__c,
              I_have_confirmed_already_upload_PSSR__c: this.emoc.I_have_confirmed_already_upload_PSSR__c,
              Action_by_PSSR__c: this.emoc.Action_by_PSSR__c,
              E_MOC_Title__c: this.emoc.E_MOC_Title__c,
              Risk_analysis_report__c: this.emoc.Risk_analysis_report__c,
              Sub_category_report__c: this.emoc.Sub_category_report__c,
              PSSR_Cate_type_Report__c: this.emoc.PSSR_Cate_type_Report__c
            }

            if (this.emoc.Budget_Estimate_Initial__c !== '') {
              emocApp.Budget_Estimate_Initial__c = this.emoc.Budget_Estimate_Initial__c
            }
            if (this.emoc.Budget_Estimate_Final__c !== '') {
              emocApp.Budget_Estimate_Final__c = this.emoc.Budget_Estimate_Final__c
            }
            let taManMapping = this.$store.state.taManMapping.filter(df => {
              // return df.Require__c === 'Optional' && df.Position__c !== ''
              return df.TA2_Indicator__c !== ''
            }).map(dm => {
              return {
                Id: dm.Id,
                E_MOC_Application__c: dm.E_MOC_Application__c,
                Position__c: dm.Position__c,
                Require__c: dm.Require__c,
                TA_Level__c: dm.TA_Level__c,
                flgDelete: dm.flgDelete,
                Position_TA3__c: dm.Position_TA3__c,
                Compliance_Standard__c: dm.Compliance_Standard__c,
                Compliance_Standard_Remark__c: dm.Compliance_Standard_Remark__c,
                Deviate_Standard__c: dm.Deviate_Standard__c,
                Deviate_Standard_Remark__c: dm.Deviate_Standard_Remark__c,
                I_have_uploaded_the_deviation_form__c: dm.I_have_uploaded_the_deviation_form__c,
                Action_by_deviated_form__c: dm.Action_by_deviated_form__c,
                N_A__c: dm.N_A__c,
                N_A_Remark__c: dm.N_A_Remark__c,
                Ta_Checklist_Result: dm.Ta_Checklist_Result,
                TA3_Name_c__c: dm.TA3_Name_c__c,
                TA3_Id__c: dm.TA3_Id__c,
                TA3_Position_c__c: dm.TA3_Position_c__c,
                I_have_finished_technical_review__c: dm.I_have_finished_technical_review__c,
                TA2_Indicator__c: dm.TA2_Indicator__c,
                TA2_Name__c: dm.TA2_Name__c,
                TA2_Position__c: dm.TA2_Position__c,
                TA3_Comment__c: dm.TA3_Comment__c,
                TA2_Expert_Field__c: dm.TA2_Expert_Field__c,
                TA3_Indicator__c: dm.TA3_Indicator__c,
                TA3_Expert_Field__c: dm.TA3_Expert_Field__c,
                TA3_s_review_has_been_completed__c: dm.TA3_s_review_has_been_completed__c,
                Action_by_TA3__c: dm.Action_by_TA3__c,
                TA2_s_review_has_been_completed__c: dm.TA2_s_review_has_been_completed__c,
                The_comment_need_to_be_verified__c: dm.The_comment_need_to_be_verified__c,
                Action_by_TA2__c: dm.Action_by_TA2__c
              }
            })
            let paramSave = {
              emocApp: emocApp,
              ta2Man: taManMapping,
              fncLocaAss: this.$store.state.emocFuncLoca.filter(df => df.Functional_Location__c !== ''),
              potentialRisk: [],
              potentialConsequence: [],
              mitigationPrecaution: []
            }
            // console.log('this.emoc.Risk_analysis_report__c', this.emoc.Risk_analysis_report__c)
            // console.log('this.emoc.Sub_category_report__c', this.emoc.Sub_category_report__c)
            // console.log('this.emoc.PSSR_Cate_type_Report__c', this.emoc.PSSR_Cate_type_Report__c)
            console.log('paramSave', paramSave)
            this.showPopupLoader()
            this.$store.dispatch('saveCreateEmoc', JSON.stringify(paramSave))
              .then(result => {
                this.closePopupLoader()
                if (result.statusSuccess) {
                  this.showPopup()
                  this.resultSuccess = result
                }
              })
            this.hasError = false
          } else {
            this.errorBagItems = Object.assign([], this.errors.items)
            if (this.checkRequiredBudgetType) {
              this.errorBagItems.push({field: 'Type_A_Maintenance_budget__c'})
            }
            if (this.checkRequiredMocType) {
              this.errorBagItems.push({field: 'Normal__c'})
            }
            if (this.checkRequiredChangeType) {
              this.errorBagItems.push({field: 'Permanent__c'})
            }
            if (this.checkRequiredCategoryType) {
              this.errorBagItems.push({field: 'Mechanical_change__c'})
            }
            if (this.checkRequiredMechanicalChange) {
              this.errorBagItems.push({field: 'Air_Cooled_Exchangers__c'})
            }
            if (this.checkRequiredInstrumentChange) {
              this.errorBagItems.push({field: 'Alarm__c'})
            }
            if (this.checkRequiredElectricalChange) {
              this.errorBagItems.push({field: 'Electrical_equipment_system__c'})
            }
            if (this.checkRequiredCivilChange) {
              this.errorBagItems.push({field: 'Buildling_and_Road__c'})
            }
            if (this.checkRequiredIdledRetired) {
              this.errorBagItems.push({field: 'Demolition__c'})
            }
            if (this.checkRequiredRiskAnalysis) {
              this.errorBagItems.push({field: 'HAZOP__c'})
            }
            if (!this.checkRamFinal || !this.checkRamInitialH) {
              this.errorBagItems.push({field: 'btnHelpTextRamFinal'})
            }
            if (this.checkRequiredMocAccomplishment) {
              this.errorBagItems.push({field: 'Yes_MOC_Accomplishment__c'})
            }
            this.hasError = true
            this.$refs.popoverErrorStd.closePopover()
          }
        })
      } else if (this.numpage === 3 && this.emoc.Functional_Override__c) {
        if (this.emoc.OOS__c) {
          this.emoc.Functional_Override_Group__c = 'Group A'
          this.emoc.Functional_Override_Category__c = 'OOS'
        } else if (this.emoc.MOS_for_ops_purpose__c) {
          this.emoc.Functional_Override_Group__c = 'Group A'
          this.emoc.Functional_Override_Category__c = 'MOS for Ops purpose'
        } else if (this.emoc.Inhibit_for_process_or_Equipment_S_D__c) {
          this.emoc.Functional_Override_Group__c = 'Group A'
          this.emoc.Functional_Override_Category__c = 'Inhibit for process or equipment S/D'
        } else if (this.emoc.Inhibit_alarm_for_maintenance__c) {
          this.emoc.Functional_Override_Group__c = 'Group B'
          this.emoc.Functional_Override_Category__c = 'Inhibit alarm for maintenance'
        } else if (this.emoc.Disable_Alarm_by_Ops_for_HW__c) {
          this.emoc.Functional_Override_Group__c = 'Group B'
          this.emoc.Functional_Override_Category__c = 'Disable alarm by Ops for HW'
        } else if (this.emoc.Inhibit_Alarm_request_for_FOX__c) {
          this.emoc.Functional_Override_Group__c = 'Group B'
          this.emoc.Functional_Override_Category__c = 'Inhibit alarm request for FOX'
        } else if (this.emoc.MOS_for_maintenance__c) {
          this.emoc.Functional_Override_Group__c = 'Group C'
          this.emoc.Functional_Override_Category__c = 'MOS for maintenance'
        } else if (this.emoc.Safeguarding_force_signal__c) {
          this.emoc.Functional_Override_Group__c = 'Group C'
          this.emoc.Functional_Override_Category__c = 'Safeguarding force signal'
        }
        Promise.all([
          this.$refs.functionalOverride.$refs.functionalLocationCmp.$validator.validateAll(),
          this.$refs.functionalOverride.$refs.group.$validator.validateAll(),
          this.$refs.functionalOverride.$refs.changeTypeCmp.$validator.validateAll(),
          this.$refs.functionalOverride.$refs.prelimRiskAssessmentCmp.$validator.validateAll(),
          this.$refs.functionalOverride.$refs.prelimRiskAssessmentCmp.$validator.validateAll('potentialConsequence'),
          this.$refs.functionalOverride.$refs.prelimRiskAssessmentCmp.$validator.validateAll('mitigationPrecaution'),
          this.$refs.functionalOverride.$refs.ramRatingCmp.$refs.ramRatingInitialCmp.$validator.validateAll(),
          !this.checkRequiredGroup
        ]).then((result) => {
          if (result.filter(df => !df).length === 0) {
            let emocApp = {
              CPM_CAPEX__c: this.emoc.CPM_CAPEX__c,
              OPM_OPEX__c: this.emoc.OPM_OPEX__c,
              Common_Workflow__c: this.emoc.Common_Workflow__c,
              Functional_Override__c: this.emoc.Functional_Override__c,
              E_MOC_Type__c: this.emoc.Common_Workflow__c ? 'Common Workflow' : 'Functional Override',
              Functional_Location__c: this.emoc.Functional_Location__c,
              Functional_Location_Description__c: this.emoc.Functional_Location_Description__c,
              Plant_of_Main_Work_Center__c: this.emoc.Plant_of_Main_Work_Center__c,
              Main_Work_Center__c: this.emoc.Main_Work_Center__c,
              Requester__c: this.emoc.Requester__c,
              Description__c: this.emoc.Description__c,
              OOS__c: this.emoc.OOS__c,
              MOS_for_ops_purpose__c: this.emoc.MOS_for_ops_purpose__c,
              Inhibit_for_process_or_Equipment_S_D__c: this.emoc.Inhibit_for_process_or_Equipment_S_D__c,
              Inhibit_alarm_for_maintenance__c: this.emoc.Inhibit_alarm_for_maintenance__c,
              Disable_Alarm_by_Ops_for_HW__c: this.emoc.Disable_Alarm_by_Ops_for_HW__c,
              Inhibit_Alarm_request_for_FOX__c: this.emoc.Inhibit_Alarm_request_for_FOX__c,
              MOS_for_maintenance__c: this.emoc.MOS_for_maintenance__c,
              Safeguarding_force_signal__c: this.emoc.Safeguarding_force_signal__c,
              Temporary__c: true,
              Change_Type__c: 'Temporary',
              Start_date__c: this.emoc.Start_date__c !== '' ? this.parseDate(this.emoc.Start_date__c) : null,
              End_date__c: this.emoc.End_date__c !== '' ? this.parseDate(this.emoc.End_date__c) : null,
              Reason_of_Work__c: this.emoc.Reason_of_Work__c,
              Potential_Risk_c__c: this.emoc.Potential_Risk_c__c,
              Other__c: this.emoc.Other__c,
              // Potential_Consequence_c__c: this.emoc.Potential_Consequence_c__c,
              // Other_Potential_Consequence__c: this.emoc.Other_Potential_Consequence__c,
              // Mitigation_Precaution_1__c: this.emoc.Mitigation_Precaution_1__c,
              // Other_Mitigation__c: this.emoc.Other_Mitigation__c,
              Mitigation_Precaution_2__c: this.emoc.Mitigation_Precaution_2__c,
              Other_Mitigation_2__c: this.emoc.Other_Mitigation_2__c,
              Mitigation_Precaution_3__c: this.emoc.Mitigation_Precaution_3__c,
              Other_Mitigation_3__c: this.emoc.Other_Mitigation_3__c,
              Notification_Type__c: 'FN',
              RAM_Rating__c: this.emoc.RAM_Rating__c,
              Assets_Severity__c: this.emoc.Assets_Severity__c,
              Environment_Severity__c: this.emoc.Environment_Severity__c,
              People_Severity__c: this.emoc.People_Severity__c,
              Product_Quality_Severity__c: this.emoc.Product_Quality_Severity__c,
              Reputation_Severity__c: this.emoc.Reputation_Severity__c,
              Assets_Likelihood__c: this.emoc.Assets_Likelihood__c,
              Environment_Likelihood__c: this.emoc.Environment_Likelihood__c,
              People_Likelihood__c: this.emoc.People_Likelihood__c,
              Product_Quality_Likelihood__c: this.emoc.Product_Quality_Likelihood__c,
              Reputation_Likelihood__c: this.emoc.Reputation_Likelihood__c,
              Assets_RAM__c: this.emoc.Assets_RAM__c,
              Environment_RAM__c: this.emoc.Environment_RAM__c,
              People_RAM__c: this.emoc.People_RAM__c,
              Product_Quality_RAM__c: this.emoc.Product_Quality_RAM__c,
              Reputation_RAM__c: this.emoc.Reputation_RAM__c,
              Assets_Remark__c: this.emoc.Assets_Remark__c,
              Environment_Remark__c: this.emoc.Environment_Remark__c,
              People_Remark__c: this.emoc.People_Remark__c,
              Product_Quality_Remark__c: this.emoc.Product_Quality_Remark__c,
              Reputation_Remark__c: this.emoc.Reputation_Remark__c,
              RAM_Total__c: this.emoc.RAM_Total__c,
              Assets_Severity_Final__c: this.emoc.Assets_Severity_Final__c,
              Environment_Severity_Final__c: this.emoc.Environment_Severity_Final__c,
              People_Severity_Final__c: this.emoc.People_Severity_Final__c,
              Product_Quality_Severity_Final__c: this.emoc.Product_Quality_Severity_Final__c,
              Reputation_Severity_Final__c: this.emoc.Reputation_Severity_Final__c,
              Assets_Likelihood_Final__c: this.emoc.Assets_Likelihood_Final__c,
              Environment_Likelihood_Final__c: this.emoc.Environment_Likelihood_Final__c,
              People_Likelihood_Final__c: this.emoc.People_Likelihood_Final__c,
              Product_Quality_Likelihood_Final__c: this.emoc.Product_Quality_Likelihood_Final__c,
              Reputation_Likelihood_Final__c: this.emoc.Reputation_Likelihood_Final__c,
              Assets_RAM_Final__c: this.emoc.Assets_RAM_Final__c,
              Environment_RAM_Final__c: this.emoc.Environment_RAM_Final__c,
              People_RAM_Final__c: this.emoc.People_RAM_Final__c,
              Product_Quality_RAM_Final__c: this.emoc.Product_Quality_RAM_Final__c,
              Reputation_RAM_Final__c: this.emoc.Reputation_RAM_Final__c,
              Assets_Remark_Final__c: this.emoc.Assets_Remark_Final__c,
              Environment_Remark_Final__c: this.emoc.Environment_Remark_Final__c,
              People_Remark_Final__c: this.emoc.People_Remark_Final__c,
              Product_Quality_Remark_Final__c: this.emoc.Product_Quality_Remark_Final__c,
              Reputation_Remark_Final__c: this.emoc.Reputation_Remark_Final__c,
              RAM_Total_Final__c: this.emoc.RAM_Total_Final__c,
              Work_Clearance_c__c: this.emoc.Work_Clearance_c__c,
              Functional_Override_Group__c: this.emoc.Functional_Override_Group__c,
              Functional_Override_Category__c: this.emoc.Functional_Override_Category__c,
              Status_Common_Report__c: this.emoc.Status_Common_Report__c,
              Status_FN_Report__c: this.emoc.Status_FN_Report__c
            }
            let potentialRisk = [{
              E_MOC_Application__c: this.emoc.E_MOC_Application__c,
              Topic__c: this.emoc.Potential_Risk_c__c,
              Comment__c: this.emoc.Other__c,
              Type__c: 'Potential Risk'
            }]
            let potentialConsequence = this.$store.state.potentialConsequence.filter(df => {
              return df.Potential_Consequence_c__c !== ''
            }).map(dm => {
              return {
                Id: dm.Id,
                flgDelete: dm.flgDelete,
                E_MOC_Application__c: dm.E_MOC_Application__c,
                Topic__c: dm.Potential_Consequence_c__c,
                Comment__c: dm.Other_Potential_Consequence__c,
                Type__c: 'Potential Consequence'
              }
            })
            let mitigationPrecaution = this.$store.state.mitigationPrecaution.filter(df => {
              return df.Mitigation_Precaution_1__c !== ''
            }).map(dm => {
              return {
                Id: dm.Id,
                flgDelete: dm.flgDelete,
                E_MOC_Application__c: dm.E_MOC_Application__c,
                Topic__c: dm.Mitigation_Precaution_1__c,
                Comment__c: dm.Other_Mitigation__c,
                Type__c: 'Mitigation/Precaution'
              }
            })
            let paramSave = {
              emocApp: emocApp,
              ta2Man: [],
              fncLocaAss: [],
              potentialRisk: potentialRisk,
              potentialConsequence: potentialConsequence,
              mitigationPrecaution: mitigationPrecaution
            }
            console.log('paramSave', paramSave)
            this.showPopupLoader()
            this.$store.dispatch('saveCreateEmoc', JSON.stringify(paramSave))
              .then(result => {
                this.closePopupLoader()
                if (result.statusSuccess) {
                  this.showPopup()
                  this.resultSuccess = result
                }
              })
            this.hasError = false
          } else {
            this.errorBagItems = Object.assign([], this.errors.items)
            if (this.checkRequiredGroup) {
              this.errorBagItems.push({field: 'OOS__c'})
            }
            this.hasError = true
            this.$refs.popoverErrorStd.closePopover()
          }
        })
      }
    },
    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    closePopupSuccess () {
      if ((!this.taReviewbyTechnologistDisabled || !this.taReviewbyOperationDisabled || !this.taReviewbyEngineerDisabled) && this.emoc.TA_Review_by_Technologist_Manager__c && this.emoc.TA_Review_by_Operation_Manager__c && this.emoc.TA_Review_by_Engineer_Manager__c) {
        this.$nextTick(() => {
          this.$refs.refModalAlterMsg.onOpen()
        })
      } else {
        this.$store.dispatch('sendMsg', {
          action: 'redirectToDetailPage',
          emocApplicationId: this.resultSuccess.emocApplicationId
        })
      }
    },
    closePopupWarnings () {
      this.saveEmoc2()
    },
    btnOkAlert () {
      this.$refs.refModalAlterMsg.onClose()
    }
  },
  components: {
    commonWorkflow,
    functionalOverride
  }
}
</script>

<style src="../assets/style.css"></style>


