<template>
  <div name="qa-qc-checklist">
    <expandable-section-std v-for="(checklist, idx1) in qaQcRemap" :title="checklist.Type__c" :closedOnStart="idx1 === 0 ? false : true" :ref="'expandDocType' + idx1" :key="'expandDocType' + idx1">
      <div class="data-frame">
        <div class="scrollbar-table">
          <table id="qa-qc-checklist">
            <tr>
              <th width="5%">No.</th>
              <!-- <th width="10%">Checklist Type</th> -->
              <th width="30%">Topic</th>
              <th width="15%">Priority
                <div class="slds-form-element__icon help-text-icon">
                  <button class="slds-button slds-button_icon custom-icon-help-text" aria-describedby="help">
                    <i class="fa fa-info-circle" aria-hidden="true"></i>
                    <span class="slds-assistive-text">Help</span>
                  </button>
                  <div class="slds-popover slds-popover_tooltip slds-nubbin_left-top help-text-display" role="tooltip" id="help" style="position:absolute;left:25px;width: 400px;text-align: left;">
                    <div class="slds-popover__body">
                      Priority A Required to be completed before the change is in-use or effective.<br>
                      Priority B Allowed to be completed after the change is in-use or effective.<br>
                    </div>
                  </div>
                </div>
              </th>
              <th width="5%">Yes</th>
              <th width="5%">No</th>
              <th width="5%">N.A</th>
              <th width="20%">Remark</th>
              <th width="7%">Checklist REV.</th>
              <th width="7%">Last modified</th>
            </tr>
            <tr v-for="(question, idx2) in checklist.Question__c">
              <!-- <td style="text-align: center;">{{question.Question_No__c}}</td> -->
              <td style="text-align: center;">{{idx2 + 1}}</td>
              <!-- <td><div class="type-list"><span :title="checklist.Type__c">{{checklist.Type__c}}</span></div></td> -->
              <td><div class="question-list"><span :title="question.Question__c">{{question.Question__c}}</span></div></td>
              <td style="text-align: center;min-width: 120px; max-width: 150px;">
                <combobox-std
                      v-model="question.Priority__c"
                      v-validate="question.No__c ? 'required' : ''"
                      data-vv-as="Potential consequence"
                      data-vv-value-path="textInputValue"
                      :name="idx1 + 'Priority__c' + idx2"
                      :options="priorityList"
                      label=""
                      :disabled="displayChecklist.view || question.N_A__c"
                      :error="checkErrorPriority(question)"
                    >
                </combobox-std>
              </td>
              <td style="text-align: center;">
                <checkbox-std v-model="question.Yes__c" :disabled="displayChecklist.view"
                              :name="idx1 + 'Yes__c' + idx2"
                              @onChange="clearAnotherCheckbox(idx1, idx2, 'Yes__c', question)"
                              :error="checkError(question)"/>
              </td>
              <td style="text-align: center;">
                <checkbox-std v-model="question.No__c" :disabled="displayChecklist.view"
                              @onChange="clearAnotherCheckbox(idx1, idx2, 'No__c', question)"
                              :error="checkError(question)"/>
              </td>
              <td style="text-align: center;">
                <checkbox-std v-model="question.N_A__c" :disabled="displayChecklist.view"
                              @onChange="clearAnotherCheckbox(idx1, idx2, 'N_A__c', question)"
                              :error="checkError(question)"/>
              </td>
              <td>
                <input-text-std style="min-width: 300px;" v-model="question.Remark__c" :disabled="displayChecklist.view"/>
              </td>
              <td><div class="column-revision">{{ displayChecklist.view ? checklist.Version__c : '' }}</div></td>
              <td><div class="column-last-modified">{{ displayChecklist.view ? checklist.CreatedBy.Name : '' }}</div></td>
            </tr>
          </table>
        </div>
      </div>
    </expandable-section-std>
    <modal-after-save id="afterSaveQaQcChecklist"
                      @close="closePopupSuccess"
                      ref="popupAfterSaveQaQcChecklist"/>
    <modal-waiting ref="popupLoader"/>
  </div>
</template>
<script>
export default {
  name: 'qa-qc-checklist',
  props: [
    'emoc', 'displayChecklist', 'callBackQaQc', 'saveEmoc'
  ],
  data () {
    return {
      resultSuccess: {},
      qaQcRemap: [],
      flgCallSave: false,
      priorityList: [
        {value: 'A', label: 'A'},
        {value: 'B', label: 'B'}
      ]
    }
  },
  mounted () {
    this.remapFieldQaQc()
  },
  computed: {
    qaQcChecklist () { return this.$store.state.qaQcChecklist },
    genDataSave () {
      let remap = []
      this.qaQcRemap.map(dm => {
        dm.Question__c.map(dmq => {
          remap.push({
            E_MOC_Application__c: dm.E_MOC_Application__c,
            Document__c: dm.Document__c,
            Sequence__c: dm.Sequence__c,
            Type__c: dm.Type__c,
            Question_No__c: dmq.Question_No__c,
            Question__c: dmq.Question__c,
            Yes__c: dmq.Yes__c || false,
            No__c: dmq.No__c || false,
            N_A__c: dmq.N_A__c || false,
            Remark__c: dmq.Remark__c || '',
            Version__c: dm.Version__c,
            Priority__c: dmq.Priority__c || ''
          })
        })
      })
      return remap
    }
  },
  watch: {
    qaQcChecklist () {
      this.remapFieldQaQc()
    }
  },
  methods: {
    remapFieldQaQc () {
      let docSeq = 99999
      let remap = []
      let indexMapping = -1
      if (this.displayChecklist.create) {
        this.qaQcChecklist.map(dm => {
          if (dm.Sequence__c !== docSeq) {
            docSeq = dm.Sequence__c
            // remap[dm.Sequence__c - 1] = {
            remap[++indexMapping] = {
              E_MOC_Application__c: dm.E_MOC_Application__c,
              Document__c: dm.Document__c,
              Type__c: dm.Type__c,
              Sequence__c: dm.Sequence__c,
              Version__c: dm.Version__c + 1,
              CreatedBy: dm.CreatedBy,
              Question__c: [{
                Question_No__c: dm.Question_No__c,
                Question__c: dm.Question__c,
                Yes__c: dm.Yes__c,
                No__c: dm.No__c,
                N_A__c: dm.N_A__c,
                Remark__c: dm.Remark__c,
                Priority__c: dm.Priority__c
              }]
            }
          } else {
            // remap[dm.Sequence__c - 1]['Question__c'].push({
            remap[indexMapping]['Question__c'].push({
              Question_No__c: dm.Question_No__c,
              Question__c: dm.Question__c,
              Yes__c: dm.Yes__c,
              No__c: dm.No__c,
              N_A__c: dm.N_A__c,
              Remark__c: dm.Remark__c,
              Priority__c: dm.Priority__c
            })
          }
        })
      } else {
        let noItem = 0
        let noType = 0
        this.qaQcChecklist.map(dm => {
          if (dm.Yes__c || dm.No__c || dm.N_A__c) {
            if (dm.Sequence__c !== docSeq) {
              noItem = 0
              docSeq = dm.Sequence__c
              remap[(++noType) - 1] = {
                E_MOC_Application__c: dm.E_MOC_Application__c,
                Document__c: dm.Document__c,
                Type__c: dm.Type__c,
                Sequence__c: dm.Sequence__c,
                Version__c: dm.Version__c,
                CreatedBy: dm.CreatedBy,
                Question__c: [{
                  Question_No__c: ++noItem,
                  Question__c: dm.Question__c,
                  Yes__c: dm.Yes__c,
                  No__c: dm.No__c,
                  N_A__c: dm.N_A__c,
                  Remark__c: dm.Remark__c,
                  Priority__c: dm.Priority__c
                }]
              }
            } else {
              remap[(noType) - 1]['Question__c'].push({
                Question_No__c: ++noItem,
                Question__c: dm.Question__c,
                Yes__c: dm.Yes__c,
                No__c: dm.No__c,
                N_A__c: dm.N_A__c,
                Remark__c: dm.Remark__c,
                Priority__c: dm.Priority__c
              })
            }
          }
        })
      }
      this.qaQcRemap = remap
    },
    clearAnotherCheckbox (idx1, idx2, field, dataRow) {
      if (field === 'Yes__c' && dataRow[field]) {
        dataRow.N_A__c = false
        dataRow.No__c = false
      }
      if (field === 'No__c' && dataRow[field]) {
        dataRow.Yes__c = false
        dataRow.N_A__c = false
      }
      if (field === 'N_A__c' && dataRow[field]) {
        dataRow.Yes__c = false
        dataRow.No__c = false
        dataRow.Priority__c = ''
      }
    },
    checkError (dataRow) {
      if (this.flgCallSave) {
        if (dataRow.Yes__c || dataRow.No__c || dataRow.N_A__c) {
          return false
        } else {
          return true
        }
      } else {
        return false
      }
    },
    checkErrorPriority (dataRow) {
      if (this.flgCallSave) {
        if (dataRow.No__c && dataRow.Priority__c === '') {
          return true
        } else {
          return false
        }
      } else {
        return false
      }
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    callSave () {
      this.flgCallSave = true
      let qaQcRemap = this.qaQcRemap
      let isError = false
      for (var idx1 = 0; idx1 < qaQcRemap.length; idx1++) {
        for (var idx2 = 0; idx2 < qaQcRemap[idx1].Question__c.length; idx2++) {
          if (!qaQcRemap[idx1].Question__c[idx2].Yes__c && !qaQcRemap[idx1].Question__c[idx2].No__c && !qaQcRemap[idx1].Question__c[idx2].N_A__c) {
            isError = true
            if (!this.$refs['expandDocType' + idx1][0].isOpen) {
              this.$refs['expandDocType' + idx1][0].toggle()
            }
            this.$nextTick(() => {
              let elements = document.getElementsByName(idx1 + 'Yes__c' + idx2)
              this.$nextTick(() => {
                setTimeout(() => elements[0].focus(), 100)
              })
            })
            break
          }
          if (qaQcRemap[idx1].Question__c[idx2].No__c && !qaQcRemap[idx1].Question__c[idx2].Priority__c) {
            isError = true
            if (!this.$refs['expandDocType' + idx1][0].isOpen) {
              this.$refs['expandDocType' + idx1][0].toggle()
            }
            this.$nextTick(() => {
              let elements = document.getElementsByName(idx1 + 'Priority__c' + idx2)
              this.$nextTick(() => {
                setTimeout(() => elements[0].focus(), 100)
              })
            })
            break
          }
        }
        if (isError) {
          break
        }
      }
      if (!isError) {
        this.showPopupLoader()
        this.$store.dispatch('saveQaQcChecklist', JSON.stringify(this.genDataSave))
          .then(result => {
            this.closePopupLoader()
            if (result.statusSuccess) {
              this.callBackQaQc()
              this.$nextTick(() => {
                this.resultSuccess = result
                this.saveEmoc()
              })
            } else {
              console.log('Error', result)
            }
          })
      }
    }
  }
}
</script>

<style scope>
  #qa-qc-checklist td, #qa-qc-checklist th {
    border: 1px solid #ddd;
    padding: 5px;
  }
  #qa-qc-checklist th {
    background-color: #fafaf9;
    padding-top: 5px;
    padding-bottom: 5px;
    text-align: center;
  }
  #qa-qc-checklist td>.type-list {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    min-width: 150px;
    max-width: 190px;
  }
  #qa-qc-checklist td>.question-list {
    overflow: hidden;
    text-overflow: ellipsis;
    /* white-space: nowrap; */
    min-width: 500px;
    max-width: 700px;
  }
  #qa-qc-checklist tr:hover {background-color: #f2f2f2;}
  #qa-qc-checklist .slds-form-element__label {display: none;}
  #qa-qc-checklist .slds-checkbox_faux {margin-right: 0px;}
  .scrollbar-table {
    overflow: auto;
  }
  .column-revision { min-width: 91px; text-align: center; }
  .column-last-modified { min-width: 200px; text-align: center; }
</style>


