<template>
  <div name="moc-accomplishment">
    <expandable-section-std title="MOC Accomplishment verification by APU operation manager" ref="expandMocAccomplishment">
      <div class="data-frame">
        <div class="row">
          <div class="col-12">
            <label>Does this implementation meet MOC objective?</label>
          </div>
        </div>
        <div class="row">
          <div class="col-sm-6">
            <checkbox-std
              v-model="emoc.Yes_MOC_Accomplishment__c"
              label="Yes"
              name="Yes_MOC_Accomplishment__c"
              :inline="true"
              :disabled="disabledMocAccomplishment"
            />
            <textarea-std v-if="emoc.Yes_MOC_Accomplishment__c"
                          v-model="emoc.Reason_MOC_Accomplishment__c"
                          data-vv-value-path="inputValue"
                          name="Reason_MOC_Accomplishment__c"
                          label="Comment"
                          :disabled="disabledMocAccomplishment"
            >
            </textarea-std>
          </div>
          <div class="col-sm-6">
            <checkbox-std
              v-model="emoc.No_MOC_Accomplishment__c"
              label="No"
              name="No_MOC_Accomplishment__c"
              :inline="true"
              :disabled="disabledMocAccomplishment"
            />
            <textarea-std v-if="emoc.No_MOC_Accomplishment__c"
                          v-model="emoc.Reason_MOC_Accomplishment__c"
                          v-validate="emoc.No_MOC_Accomplishment__c ? 'required' : ''"
                          data-vv-as="Reason"
                          data-vv-value-path="inputValue"
                          name="Reason_MOC_Accomplishment__c"
                          label="Reason"
                          :error="errors.has('Reason_MOC_Accomplishment__c')"
                          :required="true"
                          :disabled="disabledMocAccomplishment"
            >
              <template #error>
                <span>{{ errors.first('Reason_MOC_Accomplishment__c') }}</span>
              </template>
            </textarea-std>
          </div>
        </div>
      </div>
      <div v-show="checkRequiredMocAccomplishment" class="slds-form-element slds-has-error">
        <div class="slds-form-element__help">
          {{'MOC Accomplishment verification by APU operation manager is required'}}
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'moc-accomplishment',
  props: [
    'emoc',
    'disabledMocAccomplishment',
    'checkRequiredMocAccomplishment'
  ],
  watch: {
    'emoc.Yes_MOC_Accomplishment__c' (newValue) {
      if (newValue) {
        this.emoc.No_MOC_Accomplishment__c = false
      }
    },
    'emoc.No_MOC_Accomplishment__c' (newValue) {
      if (newValue) {
        this.emoc.Yes_MOC_Accomplishment__c = false
      }
    }
  }
}
</script>


